          <h1>Welcome to Jackie's OS!</h1>
          <p>Hi, I'm Panda! I'll be your guide to Jackie's OS. If you have any questions, you can always click my icon ( <img class="inlineGuideButton" src="/UI/panda-default.svg"> ) in any of the apps to learn more about Jackie's OS, what you can do on it, and why it was made!</p>


          <h2>What is Jackie's OS?</h2>
          <p>
            Jackie's OS is a operating system designed by its creator, <a href="https://jackieis.online" target="_blank">Jackie</a>, in the year 2020, for (and with) her pre-teen self in the years 2006-2008. Jackie's OS asks how things could have played out differently - both in the world of computing technology, and in the growing up of her younger self.
          <p>
            Computing technologies - from circuit boards to the graphical user interface - are embedded in a history of patriarchal, capitalist narratives. In the year 2020, the legacy of this history carries on, in both strikingly clear and quietly subtle ways. The values in these technological narratives are also ones entangled in social narratives -  values we grow up with, internalize, and carry into the world.
          </p>
          <p>
            What is it like to ask how these histories could have been different? To dream of alternative ways computers were built and designed, with values that we wish we could've seen - during the most formative moments of our pasts? How can we imagine healing our relationships to technology, by first asking what would've been healing for the child in us?
          </p>
          <p>Jackie's OS is the creator's personal response to this question, reimagining her childhood computer to ask what if the values she was surrounded by - technologically and socially - were ones she needed to see. If she had felt less pressure to overperform, to prove herself, how might she have grown up to be different?
          </p>

           <h2>Design Principles</h2>

            <p>Jackie's OS is built keeping three design principles in mind. These are values that she wish she could have internalized as a child. </p>
          <div class="designPrinciplesSection">
           

            <h3>"I have value the way I am"</h3>
            <p>A resistance to the belief that she must always prove her worth through working, being productive, and succeeding academically (especially in subjects like STEM).</p>
            <h3>"I deserve to have space for my emotions"</h3>
            <p>A resistance to the notion feelings and emotions should not be expressed, that being rational means that you can't also be emotional.</p>
            <h3>"I can have agency on my own terms"</h3>
            <p>A resistance to not having administrator access on her computer, experiencing a barrier to technology, and frustrations as the youngest child of three.</p>
          </div>

          <h4>Features in Jackie's OS</h4>
            <h2>The bedroom</h2>
            <p>The evolution of the graphical user interface - with its office metaphors - comes from a long history of patriarchal, capitalist notions of productivity. The bedroom is Jackie's rejection of these values. Instead encouraging her to prove her value through continuous productivity - what if the interface could represent values of rest and relaxation, all in the dream bedroom of her 10-year-old self?
            <h4>How to use</h4>
            <p>Click around the icons in the bedroom to open different programs.</p>
            <p></p>
            <h2>No windows, no problem</h2>
            <p>The evolution of windows in the graphical user interface comes along the notion of multitasking - that is, something that demands even more productivity out of its users. In rejection, all experiences in Jackie's OS are full-screen.</p>
            <p></p>
            <h2>(Intimately) Personal Authentication</h2>
            <p>Not having administrator access to her own computer childhood was a headache. Jackie's OS is not only designed with her own access in mind, but also imagines an authentication system that works on her own terms. Gaining entry to secret things in the system requires an intimate knowledge of things about Jackie - she's happy to share things with you, as long as you care enough to get to know her.</p>
            <h4>How to use</h4>
            <p>Give your best answer to these questions! If you're really determined, maybe try learning something more about her within Jackie's OS.</p>
            <h2 id="anchor-things">My Thingz</h2>
            <p>The traditional file system comes from the office metaphor, with dry categories and boring structure. Jackie's OS imagines <b>Thingz</b> in her computer, organized by categories that make sense to the emotional world of her younger self. <b>Thingz</b> are organized within <b>Spaces</b>, allowing Jackie's OS to hold space for emotions, as well as other things that matter to her.</p>
            <p><b>My Thingz</b> also allows for flexibility and fluidity. It rejects the idea of being boxed in, by instead embracing that Thingz can be in more than one Space at a time.</p>
            <h4>How to use</h4>
            <p>Click on spaces to open them and discover what's inside. When you hit a Thing, edit it in <b>My Sketchbook</b> or check out related Spaces.</p>
            <h2 id="anchor-sketchbook">My Sketchbook</h2>
            <p><b>My Sketchbook</b> is a testament to the main passion of 10-year-old Jackie: drawing (especially on the computer). Drawing is something she was always told was "just a hobby", whereas engineering and math were more viable career options. More than a notepad app, and also more than a paint application, the sketchbook is the central mode of creation in Jackie's OS - one that values the narrativization of her own experiences and emotions.</p>

            <h4>How to use</h4>
            <p>Browse through drawings, diary entries, and scrapbook images, in My Sketchbook. Click the "Edit" button to draw, write, or add emotional sticker tags to anything you create.</p>
            <h2 id="anchor-explorer">Cloud Explorer</h2>
            <p>The <b>Cloud Explorer</b> is the web browser on Jackie's OS, featuring the website she would have liked to have for herself when she was younger (if she only knew how to put one up....) The <b>Cloud Explorer</b> features a built in editor for her own website - responding to a wish that barriers to engaging with technology could have been lower for her past self, as a 10 year old girl in the mid 2000s.</p>
            <h4>How to use</h4>
            <p>Explore Jackie's website through jackies.world, make edits to the site, and check out her favorite sites to go on as a child.</p>
            <h2 id="anchor-messages">Postcards</h2>
            <p><b>Postcards</b> are a way to send messages in Jackie's OS, not only with friends, but also between herself - past and future. <b>Postcards</b> is encouragement for Jackie to introspect and have conversations with different version of herself - just as the design and creation of Jackie's OS is a conversation between her 2020 and 2006 selves.</p>
            <h3>Timeteller</h3>
            <p>
              What time is it? What does it matter - we're floating in the sky! We're also 10 years old, what's there even to get done? Why not open up the <i>Timeteller</i> to check your horoscope or get a suggestion for a snack?
            </p>
