<template>




  <div  ref="thisSpace" class="space" v-bind:class="{focus: focus}">
    <div v-on:click="clickOnSpace" ref="bg" class="bg" v-show="!focus" v-bind:class="{'round':roundBg}"></div>

      <div class="spacePreview" v-on:click="clickOnSpace" >
        <!-- space preview of images -->
        <span v-if=!focus>
          <img v-for="thing in thingsPreview" :key="thing.name" class="thingsInSpacePreview" v-bind:src=imgSrc(thing) v-bind:class="{isSecret:isSecret(thing)}"/>

        </span>
        <h1>{{name}}</h1>
      </div>
<!--       space in focus -->

      <!-- <transition name="fadeIn"> -->
      <div v-if="focus">
        <a class="backLink" v-on:click="goBackToPreviousSpace"> 
          &#8592; Back to {{whereFrom.name}}</a>
          <br>
<!--         If clicked on and has subspaces, display them -->
        <div v-if="hasSubspaces()">

              <space v-bind:whereFrom="thisSpaceObj" v-for="subspace in subspaces" :key="subspace.name" class="spaceContainer" v-bind:pathToNavigate="whereToGo" v-on:goToSubspaceMessage="goToSubspace" v-bind:name=subspace.name v-bind:subspaces=subspace.subspaces />
  
        </div>
        <!--     If clicked on and doesn't have subspaces display the plain contents -->
        <!-- <div v-else> -->
            <transition-group name="fade" tag="div" appear>
              <Thing v-on:clearCustomMenusMessage="clearCustomMenus"
             v-on:goToSubspaceMessage="goToSubspace"
             class="thingPreview"
             v-bind:data=thing v-for="thing in thingsInSpace" :key="thing.id"
             v-bind:menu=showMenu(thing)
             v-bind:whereFrom=name
             />
             </transition-group>

        <!-- </div> -->

      </div>
    <!-- </transition> -->

 
  </div>
</template>

<script>
import * as database from '../database.js';
import Thing from './Thing.vue';
import * as tagStructure from '../tagStructure.js';
import { fabric } from 'fabric';
import * as $ from 'jquery';

export default {
  name: 'Space',
  components: {
    Thing
  },
  watch:{
    pathToNavigate: function(newVal){
        if (newVal.nextHop == this.name){
        this.focus = true;
        this.clickOnSpace();
        this.whereToGo = this.calculateWhereToGo(newVal,this.thisNode);
      }
    }
  },
  mounted(){
    var clearCustomMenus = this.clearCustomMenus;
    window.addEventListener("click",function(e){
      if (e.target.className != "customMenu"){
        clearCustomMenus(null);
      }

    });
    let bgEl = this.$refs.bg;
    let whichAnimation = (Math.random() > .5);
    if (whichAnimation){
       $(bgEl).css("{animation:morph 8s ease-in-out infinite}");
    }
    let randomDelay = Math.random()*6;
    $(bgEl).css("animation-delay",`-${randomDelay}s`);

    this.thisSpaceObj = {el:this.$refs.thisSpace,name:this.name};

   
  },
  computed: {
   
  },
  data: function(){
    const thingsInSpace = database.getThings({tags:this.name});
    // console.log(this.name);
    // console.log("I AM SPACE : "+this.name)
    // console.log(thingsInSpace);
    //Fill thingsPreview with a few images of EVERYTHING that's in the space and/or any of its 
    var tree = tagStructure.tree;
    //Find the node for this category
    var re = new RegExp( this.name, "g");
    var thisNode = tree.find(function (node) {
      return (re).test(node.data.name)
      }).id;
    var childTags = [];
    function iterator (node) {
      childTags.push(node.data.name);
    }

    tree.find(thisNode).traverseDown(iterator);

    const thingsPreview = database.getThings({tags:childTags,limit:3});
    // let thingsPreview = [];


    let focus = (this.pathToNavigate.nextHop == this.name);
    let whereToGo = this.calculateWhereToGo(this.pathToNavigate,thisNode);

    let roundBg = (Math.random() > .5);
    console.log("refPassed");
    console.log(this.whereFrom);
    return{
      thisNode:thisNode,
      focus:focus,
      thingsInSpace: thingsInSpace,
      thingsPreview: thingsPreview,
      whereToGo: whereToGo,
      //Keep track of what Menu is open. If null, this will clear them.
      openMenu: null,
      show:true,
      roundBg:roundBg,
      hasOpenSubspaces:false,
      thisSpaceObj: null
    }
  },
  methods:{
     imgSrc: function(thing){
        let imgSrc = "";
        if (thing.canvasData != "" &&  thing.src == ""){
          let canvas =  new fabric.Canvas("canvas",{height:400,width:340});
          // console.log(canvas);
          canvas.loadFromJSON(thing.canvasData, () =>{
             let svg = canvas.toSVG();
              var svgBlob = new Blob([svg], {type: 'image/svg+xml'});
              imgSrc =  URL.createObjectURL(svgBlob);
              
          });
       
        } else if ( thing.src != ""){
          imgSrc = thing.src;
        }
        return imgSrc;
    },
        
    //Returns where to go if there is a valid path from this space
    calculateWhereToGo: function(pathToNavigate,thisNode){
      let nextHop = null;
      let nextPath = null;
      if (pathToNavigate.nextHop == this.name){
        var thisPath = pathToNavigate.nextPath;
        var tree = tagStructure.tree;
        if (thisPath.length > 0){
          nextPath = thisPath;
          nextHop = tree.find(thisNode).children[thisPath.shift()].data.name;
          nextPath = thisPath;
        }  
      }
      return ({
            nextHop:nextHop,nextPath:nextPath
      })
    },
     
    clickOnSpace: function(){
     
      // if (this.focus == false){
        this.focus = true;
      //    let thisSpace = this.$refs.thisSpace;
      // console.log(thisSpace);
      $(this.whereFrom).css('overflow-y',"hidden");
      // }
    },
    goBackToPreviousSpace: function(){
      // event.stopPropagation();
      this.focus = false;
       $(this.whereFrom).css('overflow-y',"auto");
    },
    hasSubspaces: function(){

      return this.subspaces != null;
    },
    // subspaceOpen:function(){
    //   console.log("calculating subspaceOpen");
    //   let hasOpenSubspaces = false;
    //   let thisSpaceEl = this.$refs.thisSpace;
    //   let subspaces = $(thisSpaceEl).find(".space");
    //   $(subspaces).each(function(){
    //     if ($(this).hasClass("focus")){
    //       hasOpenSubspaces = true;
    //     }
    //   });
    //   console.log(hasOpenSubspaces);
    //   return hasOpenSubspaces;
    // },
    isSecret: function(thing){
      return thing.tags.includes("secrets");
    },
    goToSubspace:function(data){
      let destination = data.destinationSpace;
      //If we need to go backward to the root to get to this space.

      if (!(this.name == destination)) {
        console.log("passing along go to subspace: "+this.name)
        this.focus = false;
        $(this.whereFrom).css('overflow-y',"auto");
        this.$emit('goToSubspaceMessage',data);
      }

    },
    //Takes in the Thing for the menu that SHOULD be open
    clearCustomMenus:function(thing){
      this.openMenu = thing;
    },
    showMenu:function(thing){
      return (this.openMenu == thing)
    }
    
  },
  props: {
    name: String,
    subspaces: Array,
    pathToNavigate: Object,
    whereFrom: Object
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

.thingsInSpacePreview{
  max-width:60px;
  max-height:60px;
  display:inline-block;
  margin:-10px;
  vertical-align:middle;
  border-radius:8px;
  /*border:2px solid;*/
}
.space{
  /*border: 2px solid black;*/
  padding:24px;
  margin:16px;
  min-width:300px;
  padding-top:60px;
  display:inline-block;


}
.space:not(.focus){
  position:relative;
  cursor: url("/UI/cursor-pointer.svg") 4 2, pointer;
  bottom:0px;
  transform:scale(1);
  transition:.2s transform;
  transition-delay:0s;

  .bg{
    background-color:#fcdbff;
    height:100%;
    width:100%;
    position:absolute;
    top:0px;
    left:0px;
    z-index:-1;
    filter:blur(10px);
    backdrop-filter: contrast(6);
    animation: morph 8s ease-in-out infinite;
    border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
    transition: all 1s ease-in-out;

  }
  .bg.round{
    border-radius:100px;
  }
}
.space:not(.focus):hover{
  /*bottom:10px;*/
  transform:scale(1.1);
}


@keyframes morph {
  0% {
      border-radius:  60% 40% 30% 70% / 60% 30% 70% 40%;
     
  } 
  50% {
      border-radius:  20% 60% 70% 90% / 50% 60% 30% 60%;
      
  }
  100% {
    border-radius:  60% 40% 30% 70% / 60% 30% 70% 40%;
  } 
}


@keyframes morph2 {
  0% {
      border-radius:  20% 20% 60% 40% / 50% 40% 20% 40%;
     
  }
  25% {
      border-radius:  30% 40% 30% 60% / 40% 50% 30% 60%;
      
  } 
  75% {
      border-radius:  60% 70% 30% 20% / 60% 50% 30% 60%;
      
  }
  100% {
     border-radius:  20% 20% 60% 40% / 50% 40% 20% 40%;
  } 
}
.backLink{
  position:absolute;
  top:48px;
  left:60px;
  cursor: url("/UI/cursor-pointer.svg") 4 2, pointer;
  font-size:20px;
  font-weight:bold;
}
.space.hasSubspaces{
  overflow-y:hidden;
}
/*.space.subspaceOpen{
  overflow-y:hidden;
  background-color:black !important;
}*/
.space.focus{
  overflow-y:auto;
  overflow-x:hidden;
  width: calc(100% - 80px);
  position:fixed;
  /*height: calc(100% - 300px);*/
  /*height:80%;*/
  height: calc(100% - 180px);
  box-shadow: 0px 2px 30px 2px rgba(100,0,200,.2);
  /*border:2px solid #da;*/
  /*background-color:white;*/
  padding:20px;
  background-color:#fcdbff;
  margin:auto;
  left:0px;
  right:0px;
  top:90px;
  /*bottom:0px;*/
  z-index:1;
  overflow-y:auto;
  border-radius:100px;
  background: -moz-linear-gradient(0deg, rgba(255,215,255,1) 0%, rgba(255,243,255,1) 100%);
  background: -webkit-linear-gradient(0deg, rgba(255,215,255,1) 0%, rgba(255,243,255,1) 100%);
  background: linear-gradient(0deg, rgba(255,215,255,1) 0%, rgba(255,243,255,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffd7ff",endColorstr="#fff3ff",GradientType=1);
}
.isSecret{
 
    filter:blur(20px);
  
}
.thingPreview{
  display:inline-block;
  margin:10px;
}

</style>
