  height: calc(100% - 260px);
  width: calc(100% - 160px);
  padding:60px;
  background: -moz-linear-gradient(0deg, rgba(255,215,255,1) 0%, rgba(255,243,255,1) 100%);
  background: -webkit-linear-gradient(0deg, rgba(255,215,255,1) 0%, rgba(255,243,255,1) 100%);
  background: linear-gradient(0deg, rgba(255,215,255,1) 0%, rgba(255,243,255,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffd7ff",endColorstr="#fff3ff",GradientType=1);
  .imgContainer{
    width:60%;
    height:calc(100% - 60px);
    /*background:blue;*/
