    /*float:left;*/
    margin-left:0px;
    /*width:100%;*/
    max-width:100%;

  }

  .infoContainer{
    float:right;
    width: calc(40% - 40px);
    text-align:left;
  }

  .tags{
    span{
      border-radius:100px;
      padding:8px 12px;
      padding-top:11px;
      margin:8px 12px;
      background-color:white;
       cursor: url("/UI/cursor-pointer.svg") 4 2, pointer;
      white-space:nowrap;
      display:inline-block;
      transform:scale(1);
      transition:.1s transform;
    }
    span:first-child{
      margin-left:0px;
    }
    span:hover{
      transform:scale(1.2);
    }
  }
}

