<template>
  <div class="sketchbook" v-bind:class="{'editingMode':editingMode, 'presentationMode':presentationMode}"
  >
<!--   <div class="pageZone left"></div>
  <div class="pageZone right"></div> -->
    <Navigation currentAppName="My Sketchbook" appId="sketchbook" v-bind:isMusicPlaying=isMusicPlaying />
    <a id="download" download="things.json"></a>
    <!-- <a href="/pdf/book.pdf" download="book.pdf">Download </a> -->
      <button class="newPageButton bold"
      v-bind:class="{'disabled':!canGenerateNewPage}"
      v-on:click="goToNewPage">New Page</button>
      <div class="toolbar" v-show="editingMode">

        <button class="toolbutton selected grab" v-on:click=grabMode>Grab</button>
        <button class="toolbutton draw" v-on:click=drawMode>Draw</button>
        <div v-show="isDrawingMode" class="colorbuttonContainer">
          <button class="toolbutton colorbutton selected-color" value="#210D3E"></button>
          <button class="toolbutton colorbutton" value="#FFFFFF"></button>
          <button class="toolbutton colorbutton" value="#FF5070"></button>
          <button class="toolbutton colorbutton" value="#FF9100"></button>
          <button class="toolbutton colorbutton" value="#FFED4B"></button>
          <button class="toolbutton colorbutton" value="#4FFFD1"></button>
          <button class="toolbutton colorbutton" value="#2896FF"></button>
          <button class="toolbutton colorbutton" value="#6C50FF"></button>
          <button class="toolbutton colorbutton" value="#FF82F7"></button>
          
        </div>
        <button class="toolbutton write" v-on:click=textMode>Write</button>
        <h3 class="stickerRollTitle">Sticker roll</h3>
        <div class="stickerRoll">
          <div v-for="tag in stickerTags" v-bind:key="tag" class="tagStickerGroup">
            <img v-bind:data='tag'
            v-on:mousedown="dragSticker"
            v-bind:src='"/tagstickers/"+tag+".svg"'
            class="tagSticker"><br>
            <b>{{tag}}</b>
       <!--      <input type="checkbox" v-bind:id='(tag+"-tag")' v-bind:name="tag"
            v-bind:value="tag" 
            v-bind:checked="editDetailsMenu.thing.tags.includes(tag)"> -->
            <!-- <label v-bind:for='(tag+"-tag")'>{{tag}}</label><br> -->
          </div>
        </div>

        <button 
        v-on:click="function(){editingMode = false}">Hide tools</button>
      </div>
      




<!--       <div v-if="editDetailsMenu.isOpen" class="editingThingMenu"
      v-bind:class="{onRight:(editDetailsMenu.orientation=='right'), onLeft:(editDetailsMenu.orientation=='left')}">
      <form v-on:submit=saveEdit>
        <h4>What should I call this creation?</h4>
   
        <input type="text" id="name" name="name" v-bind:value="editDetailsMenu.thing.name"><br>
        <h4>Categories</h4>
        <div v-for="tag in allTags" v-bind:key="tag">
          <input type="checkbox" v-bind:id='(tag+"-tag")' v-bind:name="tag"
          v-bind:value="tag" 
          v-bind:checked="editDetailsMenu.thing.tags.includes(tag)">
          <label v-bind:for='(tag+"-tag")'>{{tag}}</label><br>
        </div>
         <input type="submit" value="save" />
        <button v-on:click=cancelEdit>Cancel</button>
      </form>
      </div> -->




<!-- v-on:authenticationSuccess="openSecret" v-on:authenticationFail="denySecret" -->
       <button v-bind:class="{'disabled':(!animationFinished||currentPageNumber[0]==1)}" class="turnButton left" v-on:click="respondToPageTurn('left')">&#8592;</button>
      <button v-bind:class="{'disabled':(!animationFinished || !canTurnToRight)}" class="turnButton right" v-on:click="respondToPageTurn('right')">&#8594;</button>
      <div class="wrapper" >
         
          <div class="editBlocker left" v-show="!editingMode"></div>
          <div class="editBlocker right" v-show="!editingMode"></div>
          <button v-on:click="function(){editingMode = true}" class="editModeButton bold" v-show="!editingMode">Edit</button>
          <canvas id="currentL" v-bind:width=this.pageWidth v-bind:height=this.pageHeight></canvas>
          <canvas id="currentR" v-bind:width=this.pageWidth v-bind:height=this.pageHeight></canvas>
          <canvas id="auxL" v-bind:width=this.pageWidth v-bind:height=this.pageHeight></canvas>
          <canvas id="auxR" v-bind:width=this.pageWidth v-bind:height=this.pageHeight></canvas>

          <div class="pageTitle title title-currentL" v-show="editingMode">
          <div v-if="currentLisBlurred">
            <span class="thingName"  contenteditable="false">SECRET THING</span>
            <button v-on:click="getAccess(currentL)">Get Access</button>
          </div>
          <div v-else>
            <span class="thingName" contenteditable="true"  data="currentL" @blur="editThingName" v-on:keydown="preventEnter" v-html=currentLThing.name>
            </span>
            <button class="secretToggle" v-on:click="toggleSecret(currentLThing,'left')" v-if="currentLisSecret" >Make this Un-secret</button>
            <button class="secretToggle" v-on:click="toggleSecret(currentLThing,'left')" v-else >Make this Secret</button>
            <!-- <button v-on:click="editThingDetails(currentLThing,'right')">Edit details</button> -->
          </div>        
        </div>
        <div class="pageTitle title title-currentR" v-show="editingMode">
          <div v-if="currentRisBlurred">
             <span class="thingName"  contenteditable="false">SECRET THING</span>
            <button v-on:click="getAccess(currentR)">Get Access</button>
          </div>
          <div v-else >
            <span class="thingName"  contenteditable="true" data="currentR" @blur="editThingName" v-on:keydown="preventEnter" v-html=currentRThing.name>
            </span>
             <button class="secretToggle"  v-on:click="toggleSecret(currentRThing,'right')" v-if="currentRisSecret" >Make this Un-secret</button>
            <button class="secretToggle" v-on:click="toggleSecret(currentRThing,'right')" v-else>Make this Secret</button>
            <!-- <button class="editThingDetails" v-on:click="editThingDetails(currentRThing,'left')">Edit details</button> -->
          </div>
        </div>

      </div>

        
           <SecretAuthentication v-if="secretAuthentication.attemptedAccess" 
      v-on:authenticationSuccess="openSecret"
      v-on:authenticationFail="denySecret" />

     
  </div>
</template>


<script>
import * as database from '../database.js';
import Navigation from './Navigation.vue';
import SecretAuthentication from './SecretAuthentication';
import { fabric } from 'fabric';
import $ from 'jquery';
import * as tagStructure from '../tagStructure.js';

export default {
  name: 'Sketchbook',
  components: {
    Navigation,
    SecretAuthentication

  },
  data: function(){
    let currentPageNumber = [1,2];
    let editingMode = false;
    // let thingsInSketchbook  = database.getThings();
    if (this.applicationData.thingId > 0){
      let n = this.applicationData.thingId;
      //ID is even, that means it'll go on the right: [1,2] 
      //PAGE NUMBER SHOULD CORRESPOND TO THE NUMBER OF THE ID
      // PAGE NUMBER SHOULD BE +1 GREATER THAN ITS KEY IN thingsInSketchbook
      if (n % 2 == 0){
        currentPageNumber = [n-1,n];
      //ID is odd, that means it'll go on the left: [1,2]
      } else {
        currentPageNumber = [n,n+1];
      }
    }
    if (this.applicationData.editingMode== true){
      editingMode = true;
    }
    // console.log(thingsInSketchbook);
    return{
        speed:600,
        editingMode: editingMode,
        isDrawingMode: false,
        drawingOptions: {color:"#090914"},
        isTextMode:false,
        activeTyping: {object:null,canvas: null},
        currentL: {},
        currentLisSecret: false,
        currentRisSecret: false,
        currentLisBlurred: false,
        currentRisBlurred: false,
        currentLThing: {},
        currentR: {},
        currentRThing:{},
        auxL: {},
        auxR: {},
        canvasThatsActive: null,
        thingsInSketchbook: [],
        pageWidth: 476,
        pageHeight: 560,
        canTurnToRight: true,
        canGenerateNewPage: true,
        //current page number stores the indices that we are currently on in the book, [n,n+1]
        currentPageNumber: currentPageNumber,
        animationFinished:true,
        sketchbookInitialized:false,
        poop:"poopie",
        editDetailsMenu: {isOpen:false,orientation:"left",thing:{}},
        secretAuthentication:{
          attemptedAccess: false,
          thingToUnlock: ""
        },
        isDraggingSticker:false,
        stickerTag: "",
        presentationMode: false
        
    }
  },
  watch:{
    //This will update once for every element returned by getTHings
    thingsInSketchbook: function(){
      // console.log('thingsInSketchbook changed');
        if (!this.sketchbookInitialized){
        let n = 2;
        if (this.applicationData.thingId != null){
          //n should be one more than the id of this thing, to load the next page.
          //BUT if the thing is the last thing, n should be equal to it
          n = this.applicationData.thingId+1;
        }
        //Initialize pages when we get the piont where this thing is.
        if(this.thingsInSketchbook.length == n){
          this.sketchbookInitialized = true;
          this.initializeCurrentPages();
        }
        if (this.thingsInSketchbook.length == localStorage.getItem('numOfThings')){
          this.sketchbookInitialized = true;
          this.initializeCurrentPages();
        }
      }
    }
  },
  mounted(){


    fabric.Object.prototype.toObject = (function (toObject) {
    return function (propertiesToInclude) {
        propertiesToInclude = (propertiesToInclude || []).concat(
          ['name','tagName']
        );
        return toObject.apply(this, [propertiesToInclude]);
    };
    })(fabric.Object.prototype.toObject);

      
      this.thingsInSketchbook = database.getThings();
      let _this = this;
      //Save page changes when navigating back
      $('.goToBedroomLink').click(()=>{
        this.savePageState(this.currentL.fabricCanvas,this.currentLThing);
        this.savePageState(this.currentR.fabricCanvas,this.currentRThing);
      })
      $('.colorbutton').each(function(){
        let color = $(this).attr('value');
        $(this).css('background',color); 
      });
      $('.colorbutton').click(function(e){
        let element = e.target;
        let color = element.value;
        $('.colorbutton').not(element).removeClass('selected-color');
        $(element).addClass('selected-color')
        _this.drawingOptions.color = color;
          let leftCanvas = _this.currentL.fabricCanvas;
          let rightCanvas = _this.currentR.fabricCanvas;
          let canvases = [leftCanvas,rightCanvas,_this.auxR.fabricCanvas,_this.auxL.fabricCanvas];
          canvases.forEach(function(canvas){
            canvas.freeDrawingBrush.color = color;
        })
      });

      // window.onkeydown = function(e){
      //   _this.respondToKeyPress(e);
      // }

      window.addEventListener('keydown', this.respondToKeyPress);

     
  },

  computed:{
    allTags:function(){
      return tagStructure.allTags();
    },
    stickerTags:function(){
      let stickerTags = tagStructure.allTags().slice();
      let tagsToRemove = ['secrets','things','emotions'];
      let _this = this;
      tagsToRemove.forEach(function(val){
        stickerTags = _this.removeValueFromList(stickerTags,val);
      })
      //To remove bug issues
      stickerTags = ['sad', 'love'];
      return stickerTags;
    }
  },
  methods: {
    removeValueFromList: function(list, value){
      var index = list.indexOf(value);
        if (index > -1) {
           list.splice(index, 1);
        }
      return list;
    },
    drawMode: function(e){
      let leftCanvas = this.currentL.fabricCanvas;
      let rightCanvas = this.currentR.fabricCanvas;
      let canvases = [leftCanvas,rightCanvas];

      let thisButton = e.target;
      $('.toolbutton').not(thisButton).removeClass('selected');
      $(thisButton).addClass('selected');
      this.isDrawingMode = true;
      canvases.forEach(function(canvas){
        canvas.isDrawingMode = true;
        canvas.isDrawingMode = true;
        canvas.freeDrawingBrush.width =5;
        canvas.freeDrawingBrush.width =5;
      });
    },
    removeDrawMode: function(){
      this.isDrawingMode = false;
      this.currentL.fabricCanvas.isDrawingMode = false;
      this.currentR.fabricCanvas.isDrawingMode = false;  
    },
    grabMode: function(e){
      let thisButton = e.target;
      $('.toolbutton').not(thisButton).removeClass('selected');
      $(thisButton).addClass('selected');
      this.removeDrawMode();
      this.isTextMode = false;
      let leftCanvas = this.currentL.fabricCanvas;
      let objectsLeft = leftCanvas._objects;
      let rightCanvas = this.currentR.fabricCanvas;
      let objectsRight = rightCanvas._objects;
      objectsLeft.forEach(function(obj){
        obj.selectable = true;
        // console.log(obj);
      })
      objectsRight.forEach(function(obj){
        obj.selectable = true;
        // console.log(obj);
      })
    },
    textMode:function(e){
      var selectObjectOnCanvas = function(objectID,canvas){
        canvas.getObjects().forEach(function(o){
          if (o.id===objectID){
            canvas.setActiveObject(o);
          }
        })
      }
      let thisButton = e.target;
      $('.toolbutton').not(thisButton).removeClass('selected');
      $(thisButton).addClass('selected');
      this.removeDrawMode();
      this.isTextMode = true;
      //This should add a new text box to the screen.

      let leftCanvas = this.currentL.fabricCanvas;
      let rightCanvas = this.currentR.fabricCanvas;
      let canvases = [leftCanvas,rightCanvas];
      let _this = this;
      canvases.forEach(function(canvas){
        let objects = canvas._objects;
        objects.forEach(function(obj){
          if (obj.type != 'i-text'){
            obj.selectable = false;
          }
        });
        //add the event listener
        canvas.on('mouse:down', function(options) {
          // console.log(options);
          let coords = options.pointer;
            var text = new fabric.IText('', { id:this.id, left: coords.x, top: coords.y,inCompositionMode:true,fontFamily:'Jackie Comic'});
          //Case 1: we are over blank area - create new text
          if (options.target == null ){
            
            if (_this.activeTyping.object == null){
              canvas.add(text);
              selectObjectOnCanvas(text.id,canvas);
              text.enterEditing();
              _this.activeTyping = {object:text,canvas:canvas};
            } else {
              _this.activeTyping = {object:null,canvas:null};
            }
            
          //Case 2: we clicked an element that's not text
          } else if (options.target.text == null){
            if (_this.activeTyping.object == null){
              canvas.add(text);
              selectObjectOnCanvas(text.id,canvas);
              text.enterEditing();
             _this.activeTyping = {object:text,canvas:canvas};
            } else {
               _this.activeTyping = {object:null,canvas:null};
            }
          //Case 3: We clicked on text
          } else {
           _this.activeTyping = {object:options.target,canvas:canvas};
            // console.log("clicking on text");
          }
        })
      })
    },
    dragSticker(e){
      this.currentL.element = document.getElementById("currentL");
      this.currentR.element = document.getElementById("currentR");
      this.isDraggingSticker = true;
      e.stopPropagation();
      e.preventDefault();
      var element = e.target;
      let tagName = element.attributes.data.value;
      this.stickerTag = tagName;

      var offset = $(element).offset();
      var relativeX = (e.pageX - offset.left);
      var relativeY = (e.pageY - offset.top);
      var newStickerEl = $(element).clone();
      $(newStickerEl).addClass("newStickerEl");
      $(newStickerEl).css("left", e.pageX - relativeX);
      $(newStickerEl).css("top",e.pageY - relativeY);
      $("body").append(newStickerEl);
      let continueDragging = function(e){
        $(newStickerEl).css("left", e.pageX - relativeX);
        $(newStickerEl).css("top",e.pageY - relativeY);

      }
      window.addEventListener('mousemove', continueDragging);
      let _this = this;
      window.addEventListener('mouseup',function(e){
        if (_this.isDraggingSticker){
          //Go into the active canvases and place a new object there
          let mouse = {x:e.pageX,y:e.pageY};
          let canvasObjs = [_this.currentL,_this.currentR];
          let isDropValid = false;
          canvasObjs.forEach(function(canvasObj){
            let canvasEl = canvasObj.element;
            let bounding = canvasEl.getBoundingClientRect();
            let isBlurred = true;
            if (canvasEl.id == "currentR"){
               isBlurred = _this.currentRisBlurred;
            } else if (canvasEl.id == "currentL") {
              isBlurred = _this.currentLisBlurred;
            }
            if ((mouse.x > bounding.x && mouse.x < bounding.x + bounding.width) && (mouse.y > bounding.y && mouse.y < bounding.y + bounding.height) && !isBlurred){
              isDropValid = true;
              let canvas = canvasObj.fabricCanvas;
              let dropPos = {
                x: mouse.x - bounding.left - relativeX,
                y: mouse.y - bounding.top - relativeY
              };

              let stickerURL = `./tagstickers/${_this.stickerTag}.svg`;
              fabric.Image.fromURL(stickerURL, (oImg) =>{
                oImg.width = 50;
                oImg.height = 50;
                oImg.left = dropPos.x;
                oImg.top = dropPos.y;
                oImg.name = "stickerTag";
                oImg.tagName =_this.stickerTag;
                canvas.add(oImg);
              });

              //add it to the object
              let thingToModify;
              if (canvasEl.id == 'currentL'){
                thingToModify = _this.currentLThing;
              } else if (canvasEl.id == 'currentR'){
                
                thingToModify = _this.currentRThing;
              }
              thingToModify = JSON.parse(JSON.stringify(thingToModify));
              if (!thingToModify.tags.includes(_this.stickerTag)){
                thingToModify.tags.push(_this.stickerTag);
                database.updateData(thingToModify);
                _this.thingsInSketchbook = database.getThings();
                if (canvasEl.id == 'currentL'){
                  _this.currentLThing = thingToModify;
                } else if (canvasEl.id == 'currentR'){
                  _this.currentRThing = thingToModify;
                }
              }
              

              $('.newStickerEl').remove();
            }
          })
          if (isDropValid == false){
            $('.newStickerEl').fadeOut("normal", function(){$(this).remove()});
          }
          window.removeEventListener('mousemove', continueDragging);
          _this.isDraggingSticker = false;
        }
      })


 
    },
    goToNewPage: function(){
      //Should go to the last two pages (that have data) in the sketchbook.
      // If there's only an odd number of things, create 1 blank thing then go
      //If last two pages have things in them, then turn the page

      let things = this.thingsInSketchbook;
      let numOfThings = things.length;
      //ODD NUMBER OF THINGS: Create 3 blank thing, then go
      if (numOfThings % 2 != 0){
        // console.log("num things is odd");
        this.createBlankThings(3,true);
        this.canGenerateNewPage = false;
        this.goToPage(numOfThings+2,numOfThings+3);
      //EVEN NUMBER OF THINGS
      } else {
        // console.log("num things is even");
        let pageNum = [things.length-1,things.length];
        let leftPageCanvasData = things[pageNum[0]-1].canvasData;
        let rightPageCanvasData = things[pageNum[1]-1].canvasData;
        // At least one of the pages is full of something
        if ((leftPageCanvasData != "" && leftPageCanvasData != '{"version":"3.6.3","objects":[]}') || (rightPageCanvasData != "" &&  rightPageCanvasData != '{"version":"3.6.3","objects":[]}')) {
          this.createBlankThings(2,true);
          this.canGenerateNewPage = false;
          this.goToPage(pageNum[0]+2,pageNum[1]+2);
        //Go to those pages because they're blank
        } else {
          
          //but only if we are currently NOT ON IT
          if (this.currentPageNumber[0] != pageNum[0]){
            this.canGenerateNewPage = false;
            // console.log("CALLING GO TO PAGE");
            this.goToPage(pageNum[0],pageNum[1]);
          } else {
            // else if we are on it, and something has modified the drawing
            // let us add two pages
            this.createBlankThings(2,true);
            this.canGenerateNewPage = false;
            this.goToPage(pageNum[0]+2,pageNum[1]+2);
          }
          
        }

      }
      
      // let leftPageCanvas = JSON.parse(things[pageNum[0]-1].canvasData);
      // let rightPageCanvas = JSON.parse(things[pageNum[1]-1].canvasData);
      // let isLeftBlank = (leftPageCanvas.objects.length == 0);
      // let isRightBlank = (rightPageCanvas.objects.length == 0);
      // if ( isLeftBlank && isRightBlank){
      //   this.createBlankThings(2);
      //   this.goToPage(pageNum[0]+2,pageNum[1]+2);
      //   //Add blank pages in
      //   // this.canTurnToRight = false
      // }
      // // this.goToPage(this.currentPageNumber[0],this.currentPageNumber[1]);
      // else {
      //   this.goToPage(pageNum[0],pageNum[1]);
      // }
      
    },
    initializeCurrentPages: function() {
      // console.log("INITIALIZING CURRENT PAGES");
      this.currentLThing = this.thingsInSketchbook[this.currentPageNumber[0]-1];
      this.currentRThing = this.thingsInSketchbook[this.currentPageNumber[1]-1];

      //Handle the case where we are opening the last thing and it's an odd element

      if (this.currentRThing == null){
        this.currentRThing = {
          name: "New creation",
          src: "",
          tags: [],
          canvasData: ""
        };
      }

      this.currentLisSecret = this.thingIsSecret(this.currentLThing);
      this.currentLisBlurred = this.thingIsSecret(this.currentLThing);
      this.currentRisSecret = this.thingIsSecret(this.currentRThing);
      this.currentRisBlurred = this.thingIsSecret(this.currentRThing);

      let optionsCurrentL = {position:"absolute",top:0,left:0,containerClass:"canvas-container  container-currentL"};
      let optionsCurrentR = {position:"absolute",top:0,left:0,containerClass:"canvas-container  container-currentR"};
      let optionsAuxL = {position:"absolute",top:0,left:0,containerClass:"canvas-container  container-auxL"};
      let optionsAuxR = {position:"absolute",top:0,left:0,containerClass:"canvas-container  container-auxR"};

      this.currentL.fabricCanvas = new fabric.Canvas('currentL',optionsCurrentL);
      this.currentL.fabricCanvas.isDrawingMode = this.isDrawingMode;
      this.currentL.fabricCanvas.freeDrawingBrush.width = 5;

      this.currentR.fabricCanvas = new fabric.Canvas('currentR',optionsCurrentR);
      this.currentR.fabricCanvas.isDrawingMode = this.isDrawingMode;
      this.currentR.fabricCanvas.freeDrawingBrush.width = 5;

      this.updateFabricCanvasListeners();

      this.auxR.fabricCanvas = new fabric.Canvas('auxR',optionsAuxR);
      this.auxR.fabricCanvas.isDrawingMode = false;
      this.auxL.fabricCanvas = new fabric.Canvas('auxL',optionsAuxL);
      this.auxL.fabricCanvas.isDrawingMode = false;
      this.auxR.fabricCanvas.freeDrawingBrush.width = 5;
      this.auxL.fabricCanvas.freeDrawingBrush.width = 5;

      $(".canvas-container").css("position","absolute");
      $(".container-currentL .upper-canvas").css({position:"absolute",transform:`translateX(-${this.pageWidth}px)`});
      $(".container-auxL .upper-canvas").css({
        position:"absolute",
        transform:`translateX(-${this.pageWidth}px)`});

      if (this.currentLisSecret){
         $(".container-currentL .upper-canvas").css("pointer-events","none");
      }
      if (this.currentRisSecret){
         $(".container-currentR .upper-canvas").css("pointer-events","none");
      }

      this.currentL.element = document.getElementById("currentL");
      this.currentR.element = document.getElementById("currentR");

      this.auxL.element = document.getElementById("auxL");
      this.auxR.element = document.getElementById("auxR");

      $(this.currentL.element).parent().css("pointer-events","auto");
      $(this.currentR.element).parent().css("pointer-events","auto");
      $(this.auxR.element).parent().css("pointer-events","none");
      $(this.auxL.element).parent().css("pointer-events","none");

      //Load canvas data, otherwise load it with an initial image
      if (this.currentLThing.canvasData != ""){
        // console.log("loading canvas data for currentL")
        this.loadCanvasData(this.currentL.fabricCanvas,this.currentLThing.canvasData,this.currentLisSecret)
        // this.currentL.fabricCanvas.loadFromJSON(this.currentLThing.canvasData);
      } else {
         // console.log("loading image data for currentL")
        this.initializeCanvas(this.currentL.fabricCanvas,this.currentLThing,this.currentLisSecret);
      }
      if (this.currentRThing.canvasData != ""){
         // console.log("loading canvas data for currentR")
        this.loadCanvasData(this.currentR.fabricCanvas,this.currentRThing.canvasData,this.currentRisSecret)
      } else {
         // console.log("loading image data for currentR")
        this.initializeCanvas(this.currentR.fabricCanvas,this.currentRThing,this.currentRisSecret);
      }

      
     
    },

    initializeCanvas: function(canvas,thingData,isSecret) {
      // console.log('initializing canvas');
      let imgSrc = thingData.src;
      let tags = thingData.tags;
      // let _this = this;
      if (imgSrc !== ""){
        fabric.Image.fromURL(imgSrc, (oImg) =>{
          let imgWidth = oImg.width;
          let imgHeight = oImg.height;
          // scale image down, and flip it, before adding it onto canvas
          let scale = Math.min(this.pageWidth/imgWidth, this.pageHeight/imgHeight);
          if (isSecret){
            var blurFilter = new fabric.Image.filters.Blur({
              blur: 1
            });
            oImg.filters.push(blurFilter);
          }
          // oImg.scale(scale*.9);
          oImg.scale(scale);
          oImg.applyFilters();
          // oImg.left = 25;
          // oImg.top = 25;
          oImg.left = 0;
          oImg.top = 0;
          canvas.add(oImg);
          canvas.sendToBack(oImg);
          canvas.renderAll();
        });
      }
      if (tags.length > 0){
        // console.log("there are tags here");
        //Randomly place the stickers with the appropriate tags
        tags.forEach(function(tagName){
          // console.log('considering tagname: '+tagName);
          let tagSrc = `./tagstickers/${tagName}.svg`;
          if (tagName == "sad" || tagName == "love"){
            // console.log('found a tag: '+tagName);
              fabric.Image.fromURL(tagSrc, (oImg) =>{
              // scale image down, and flip it, before adding it onto canvas
              if (isSecret){
                var blurFilter = new fabric.Image.filters.Blur({
                  blur: 1
                });
                oImg.filters.push(blurFilter);
              }
              oImg.applyFilters();
              oImg.left = 80+Math.random()*200;
              oImg.top = 80+Math.random()*500;
              oImg.width = 50;
              oImg.height = 50;
              oImg.name = "stickerTag";
              oImg.tagName =tagName;

              // console.log(oImg);
         
              canvas.add(oImg);
              canvas.bringToFront(oImg);

            });
          }
        })
      }
    },
    //Like the turn page function but goes from your current page to another page
    goToPage: function(toPageL_num,toPageR_num){
      // console.log(toPageL_num,toPageR_num);
        if (this.animationFinished){
          this.currentL.element = document.getElementById("currentL");
          this.currentR.element = document.getElementById("currentR");
          this.auxL.element = document.getElementById("auxL");
          this.auxR.element = document.getElementById("auxR");
          this.animationFinished = false;
          // console.log("go to page function");
          // let fromPageL_num = this.currentPageNumber[0];
          let fromPageR_num = this.currentPageNumber[1];
          //It is navegable by right
          if (toPageR_num % 2 != 0){
            // console.log("error, tp page R is not even");
            return;
          }
          if (toPageR_num >= fromPageR_num + 2){
            // console.log('we need to go right for it');
            this.currentPageNumber[0] = toPageL_num - 2;
            this.currentPageNumber[1] = toPageR_num - 2;
            this.turnPage('right');
          //else we can go left to get it
          } else if (toPageR_num <= fromPageR_num - 2){
            // console.log('we need to go left for it');
            this.currentPageNumber[0] = toPageL_num + 2;
            this.currentPageNumber[1] = toPageR_num + 2;
            this.turnPage('left');
          }
      }
    },
    turnPage: function(direction){
      var things = this.thingsInSketchbook;
      // console.log('turning page with current page numbres:')
      // console.log(this.currentPageNumber);
      // console.log(this.currentL);
      if (direction == "right" && (things.length >= this.currentPageNumber[1] + 2) || direction == "left" && (1 <= this.currentPageNumber[0] - 2)){
        // this.canTurnToRight = true;
        this.activeTyping = {object: null,canvas:null};
        var pageN,pageM;
        var turningElements= [];
        if (direction == "right"){
            pageN = this.currentPageNumber[1] + 1;
            pageM = this.currentPageNumber[1] + 2;
            turningElements = [this.currentR.element,this.auxL.element];
            turningElements[0].className = "turning turning-right";
            turningElements[1].className = "turning turning-right";
        } else if (direction == "left"){
          // console.log('turning left');
            this.canTurnToRight = true;
            this.canGenerateNewPage = true;
            pageN = this.currentPageNumber[0] - 2;
            pageM = this.currentPageNumber[0] - 1;  
            // console.log(pageN,pageM);
            turningElements = [this.currentL.element,this.auxR.element];
            turningElements[0].className = "turning turning-left";
            turningElements[1].className = "turning turning-left";
        }

        var thing1_original = this.currentLThing;
        var thing2_original = this.currentRThing;
        var thing1_new = things[pageN-1];
        var thing2_new = things[pageM-1];
        if (thing1_new != null){
          //We have new thing1 available
          if (thing1_new.canvasData != ""){
            // console.log("LOADING THING1 FROM CANVAS")
            this.loadCanvasData(this.auxL.fabricCanvas,thing1_new.canvasData,this.thingIsSecret(thing1_new))
          } else {
             // console.log("LOADING THING1 FROM IMG")
   
            this.initializeCanvas(this.auxL.fabricCanvas,thing1_new,this.thingIsSecret(thing1_new));
          }
        } 
        if (thing2_new != null){
          // console.log(thing2_new);
          if (thing2_new.canvasData != ""){
            // console.log("LOADING THING2 FROM CANVAS")
            this.loadCanvasData(this.auxR.fabricCanvas,thing2_new.canvasData,this.thingIsSecret(thing2_new))
          } else {
            // console.log("LOADING THING2 FROM IMG")

            this.initializeCanvas(this.auxR.fabricCanvas,thing2_new,this.thingIsSecret(thing2_new));
          }
        }
        let currentLCanvasCopy = JSON.parse(JSON.stringify(this.currentL.fabricCanvas));
        let currentRCanvasCopy = JSON.parse(JSON.stringify(this.currentR.fabricCanvas));
        let thing1_copy = JSON.parse(JSON.stringify(thing1_original));
        let thing2_copy = JSON.parse(JSON.stringify(thing2_original));
        this.savePageState(currentLCanvasCopy,thing1_copy);
        this.savePageState(currentRCanvasCopy,thing2_copy);

        window.setTimeout( () => {
          turningElements[0].className = "";
          turningElements[1].className = "";
          //Switch the current pages and the aux pages (aux pages now becomes the current page)
          this.switchCurrentAndAux(thing1_new,thing2_new,direction);
          this.animationFinished = true;
          this.currentPageNumber = [pageN,pageM];

        },1300);

      } else if ( direction == "right" && (things.length >= this.currentPageNumber[1] + 1)){
        // console.log("we need to create 1 blank thing and turn page right")
        this.createBlankThings(1);
        this.animationFinished = true;

      }
      else if (direction == "right"){
        // console.log("need to create 2 blank things and turn page right");
        this.createBlankThings(2);
        this.animationFinished = true;
        this.canTurnToRight = false;
        
      } 
      else {
        this.animationFinished = true;
        // console.log("unable to turn page");
      }

    },
    switchCurrentAndAux:function(thing1_new,thing2_new,direction){

      // console.log("switching current and aux");
      // console.log(this.currentL); 
      this.currentR.element.id = "auxR";
      this.currentL.element.id = "auxL";
      $(this.currentR.element).parent().addClass("container-auxR").removeClass("container-currentR");
      $(this.currentL.element).parent().addClass("container-auxL").removeClass("container-currentL");
      // console.log("after switch")
      // console.log(this.currentL); 
      $(this.currentL.element).parent().css("pointer-events","none");
      $(this.currentR.element).parent().css("pointer-events","none");

      this.auxR.element.id = "currentR";                 
      this.auxL.element.id = "currentL";
     
      $(this.auxR.element).parent().removeClass("container-auxR").addClass("container-currentR ");

      $(this.auxR.element).parent().css("pointer-events","auto");
      $(this.auxL.element).parent().css("pointer-events","auto");
      $(this.auxL.element).parent().removeClass("container-auxL").addClass("container-currentL");

      //Clear the Fabric canvases for current R and L
      this.clearImage(this.currentR.fabricCanvas);
      this.clearImage(this.currentL.fabricCanvas);
      var prevCurrentR = this.currentR.fabricCanvas;
      var prevCurrentL = this.currentL.fabricCanvas;
      var prevAuxL = this.auxL.fabricCanvas;
      var prevAuxR = this.auxR.fabricCanvas;

      let isLeftBlank = (prevAuxL._objects.length == 0);
      let isRightBlank = (prevAuxR._objects.length == 0);
      if (direction == "right" && isLeftBlank && isRightBlank){
        this.canTurnToRight = false
        this.canGenerateNewPage = false
      }
      this.currentR.fabricCanvas = prevAuxR;
      this.currentL.fabricCanvas = prevAuxL;
      this.currentR.fabricCanvas.isDrawingMode = this.isDrawingMode;
      this.currentL.fabricCanvas.isDrawingMode = this.isDrawingMode;


      this.currentLThing = thing1_new;
      this.currentRThing = thing2_new;


      this.auxL.fabricCanvas = prevCurrentL;
      this.auxR.fabricCanvas = prevCurrentR;
      this.auxL.fabricCanvas.isDrawingMode = false;
      this.auxR.fabricCanvas.isDrawingMode = false;

      if (this.thingIsSecret(this.currentLThing)){
        this.currentLisSecret = true;
        this.currentLisBlurred = true;
        $(".container-currentL .upper-canvas").css("pointer-events","none");
         $(".container-auxL .upper-canvas").css("pointer-events","none");
      } else {
        this.currentLisSecret = false;
        this.currentLisBlurred = false;
        $(".container-currentL .upper-canvas").css("pointer-events","auto");
         $(".container-auxL .upper-canvas").css("pointer-events","none");
      }
      if (this.thingIsSecret(this.currentRThing)){
        this.currentRisSecret = true;
        this.currentRisBlurred = true;
        $(".container-currentR .upper-canvas").css("pointer-events","none");
         $(".container-auxR .upper-canvas").css("pointer-events","none");
      } else {
        this.currentRisSecret = false;
        this.currentRisBlurred = false;
        $(".container-currentR .upper-canvas").css("pointer-events","auto");
        $(".container-auxR .upper-canvas").css("pointer-events","none");
      }
      this.updateFabricCanvasListeners();

      // console.log(this.currentL, this.currentR);
    },
    updateFabricCanvasListeners: function(){
      var _this = this;
      function modifiedCallback(e){
        // console.log(e.target.type);
        if (e.target.type == "path" || e.target.type == "i-text"){
          // console.log("setting turning to right true")
          _this.canTurnToRight = true;
          _this.canGenerateNewPage = true;
          // _this.currentR.fabricCanvas
        }
      }

      function selectionCallback(e){
        // console.log(e);
        // console.log(e.target);
        //We have a canvas for the first time
        if (e.target && _this.canvasThatsActive == null){
          _this.canvasThatsActive = e.target;
        }
        //We have cliked on a different canvas
        else if (e.target != _this.canvasThatsActive){
          let prevActiveCanvas = _this.canvasThatsActive.canvas;
          prevActiveCanvas.discardActiveObject();
          prevActiveCanvas.requestRenderAll();
          _this.canvasThatsActive = e.target;
        }
      
      }

      function preventDragOffCanvas(e){
        var target = e.target;
        let height = target.height;
        let width = target.width;
        // console.log(target);
        if (target.scaleX !== 1){
          width = target.scaleX*width
        }
        if (target.scaleY !== 1){
          height = target.scaleY*height;
        }
        let top = target.top;
        let left = target.left;
        // console.log(this);
        // console.log(e);
        let rightBound = this.width;
        let bottomBound = this.height;
        let modified = false;
        if (top < 0){
          // console.log("prevent moving off top")
          top=0;
          modified = true;
        }
        if (top+height > bottomBound){
          // console.log("prevent moving off bottom")
          top= bottomBound - height;
          modified = true;
        }
        if (left < 0){
          // console.log("prevent moving off left")
          left = 0;
          modified = true;
        }
        if (left+width > rightBound){
          // console.log("prevent moving off right")
          left = rightBound - width;
          modified = true;
        }
        if (modified){
          target.set("left",left);
          target.set("top",top);
          target.setCoords();
        }
      }



      let canvases = [this.currentR.fabricCanvas,this.currentL.fabricCanvas];

      canvases.forEach(function(canvas){
        canvas.on('object:added',modifiedCallback);
        canvas.on('selection:created',selectionCallback);
        canvas.on('object:moving',preventDragOffCanvas);
        // canvas.on('object:scaling',_this.adjustObjectSize);
      })

    },
