  //   console.log("object scaling");

  //   var target = e.target;
  //   if (!target || target.type !== 'rect') {
  //       return;
  //   }
  //   var sX = target.scaleX;
  //   var sY = target.scaleY;
  //   target.width *= sX;
  //   target.height *= sY;
  //   target.scaleX = 1;
  //   target.scaleY = 1;
  //   target.dirty = true;
  // },

    deleteSelectedFabricObjs: function(){
    this.currentL.element = document.getElementById("currentL");
    this.currentR.element = document.getElementById("currentR");
      let canvases = [this.currentL, this.currentR];
      let _this = this;
      canvases.forEach(function(canvas){
        let fabricCanvas = canvas.fabricCanvas;
        let canvasEl = canvas.element;
        let activeObjects = fabricCanvas.getActiveObjects();
        activeObjects.forEach(function(object){
          //add logic to remove sticker data
          fabricCanvas.remove(object);
          if (object.name == 'stickerTag'){
            let tagName = object.tagName;
            // console.log('tagName to remove:'+tagName);
            //Detect if it's the last sticker of that type
            let removedAllOfThisType = true;
            let remainingObjects = fabricCanvas._objects;
            remainingObjects.forEach(function(obj){
              //there is still another of this 
              if (obj.name == 'stickerTag' && obj.tagName == tagName){
                removedAllOfThisType = false;
              }
            })
            if (removedAllOfThisType){
              // console.log("we removed all of the sticker tag, so remove from database");
              let thingToModify;
              if (canvasEl.id == 'currentL'){
                thingToModify = _this.currentLThing;
              } else if (canvasEl.id == 'currentR'){
                thingToModify = _this.currentRThing;
              }
              thingToModify = JSON.parse(JSON.stringify(thingToModify));
              thingToModify.tags = _this.removeValueFromList(thingToModify.tags,tagName);
              database.updateData(thingToModify);
              _this.thingsInSketchbook = database.getThings();
              if (canvasEl.id == 'currentL'){
                _this.currentLThing = thingToModify;
              } else if (canvasEl.id == 'currentR'){
                _this.currentRThing = thingToModify;
              }
            }
          }
        });
        _this.canvasThatsActive = null;
  
      });
    },

    clearImage: function(canvas){
        canvas.clear();

    },
    //Param: fabricCanvas and page number
    savePageState: function(fabricCanvas,thing){
        var canvasData = this.fabricCanvasToData(fabricCanvas);
        var thisThing = thing;
        //Only save page state if it's been mutated since last time
        if (canvasData != thing.canvasData){
          thisThing.canvasData = canvasData;
          database.updateData(thisThing, thisThing.id);
          this.thingsInSketchbook = database.getThings();          
        }

    },
    fabricCanvasToData: function(fabricCanvas){
      return JSON.stringify(fabricCanvas);
    }
    ,
    // //Function that runs when new page is modified.
    // detectModification: function(){
    //   if (this.animationFinished){
    //     jijij
    //   }
    // },
    //funciton to create blank things. n is the number of things to create 
    createBlankThings: function(n,dontTurn){
      var addBlankThings = (newItem, n, _this) => {
        let transaction = database.db.transaction(['things_os'], 'readwrite');
        // call an object store that's already been added to the database
        let objectStore = transaction.objectStore('things_os');
        let allThings = [];
        // Make a request to add our newItem object to the object store
        var howManyRequestsFinished = 0;
        for (var i=0;i<n;i++){
          let request = objectStore.add(newItem);
          request.onsuccess = function(){
            let getAllRequest = objectStore.getAll();
            getAllRequest.onsuccess = () => {
              allThings = getAllRequest.result;
              howManyRequestsFinished++;
              if (howManyRequestsFinished == n){
                  _this.thingsInSketchbook = allThings;
                  if (!dontTurn){
                    _this.turnPage('right');
                  }
              }
            }
          }
        }
      }
      let newThing = {
        name: "New creation",
        src: "",
        tags: [],
        canvasData: ""
      };
      addBlankThings(newThing,n,this);
    },
    loadCanvasData: function(fabricCanvas,canvasData,isSecret){
      // console.log("loading data");
      // console.log(canvasData);
   
      fabricCanvas.loadFromJSON(canvasData, function(){
        let objects = fabricCanvas._objects;  
        var filter = new fabric.Image.filters.Blur({
            blur: 1
        });
        //Iterate through objects in canvas data
        for (var i=0; i< objects.length; i++){
          let object = objects[i];
          if (object.filters != null){
            object.filters = [];
            if (isSecret){
              object.filters.push(filter);
              if(object.type == "path"){
                // console.log("OBJECT IS PATH TYPE");
                // console.log(object);
                // object.setShadow({color: 'rgba(0,0,0,0)',blur:20})
              }
            }
            object.applyFilters();
          }
        }
        fabricCanvas.renderAll();
      });

      
    },
    respondToKeyPress: function(e){
      if (e.which == 8){
        this.deleteSelectedFabricObjs();
      }
      // if (e.which == 192){
      //   this.presentationMode = !this.presentationMode;
      // }
      if (this.presentationMode && this.animationFinished 
        && (e.keyCode == 37 || e.keyCode == 39)) {
          this.currentL.element = document.getElementById("currentL");
          this.currentR.element = document.getElementById("currentR");

          this.auxL.element = document.getElementById("auxL");
          this.auxR.element = document.getElementById("auxR");
          switch(e.keyCode){
            case 37:
                this.animationFinished = false;
                // console.log('left key');
                //Check that we can actually go left
                this.turnPage("left");
              break;
            case 39:
                if (this.canTurnToRight && this.canGenerateNewPage){
                  this.animationFinished = false;
                  // console.log('right key');
                  this.turnPage("right");
                }
              break;
        }
      }
      //caps lock -> download myThings data
      // if (e.which == 20){
      //   let obj = {'initialThings':this.thingsInSketchbook}
      //   var dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(obj));
      //   var dlAnchorElem = document.getElementById('download');
      //   dlAnchorElem.setAttribute("href",dataStr);
      //   dlAnchorElem.setAttribute("download", "things.json");
      //   dlAnchorElem.click();
      // }
    },
    respondToPageTurn: function(direction){
      if (this.animationFinished){
        this.currentL.element = document.getElementById("currentL");
        this.currentR.element = document.getElementById("currentR");
        this.auxL.element = document.getElementById("auxL");
        this.auxR.element = document.getElementById("auxR");
        this.animationFinished = false;
        this.turnPage(direction);
      }
    },
    editThingDetails: function(thingToEdit,orientation){
