      // console.log(newName);

      // console.log(currentThing);
    },
    preventEnter:function(e){
      if (e.which ===13){
        e.preventDefault();
        e.target.blur();
      }
    },
    toggleSecret: function(whichThing,direction){
      let thingToModify =  JSON.parse(JSON.stringify(whichThing));
      //Change to NOT secret
      if (this.thingIsSecret(whichThing)){
        let index = thingToModify.tags.indexOf("secrets")
        thingToModify.tags.splice(index,1);
        if (direction== 'right'){
          this.currentRisSecret = false;
          this.currentRisBlurred = false;
          this.loadCanvasData(this.currentR.fabricCanvas,this.currentRThing.canvasData,this.currentRisBlurred)
          this.currentRThing = thingToModify;
        } else if (direction == 'left') {
          this.currentLisSecret = false;
          this.currentLisBlurred = false;
          this.loadCanvasData(this.currentL.fabricCanvas,this.currentLThing.canvasData,this.currentLisBlurred)
          this.currentLThing = thingToModify;
        }
      //Change to secret
      } else {
        thingToModify.tags.push('secrets');
        if (direction== 'right'){
          this.currentRisSecret = true;
          this.currentRisBlurred = true;
          this.loadCanvasData(this.currentR.fabricCanvas,this.currentRThing.canvasData,this.currentRisBlurred)
          this.currentRThing = thingToModify;
        } else if (direction == 'left') {
          this.currentLisSecret = true;
          this.currentLisBlurred = true;
          this.loadCanvasData(this.currentL.fabricCanvas,this.currentLThing.canvasData,this.currentLisBlurred)
          this.currentLThing = thingToModify;
        }
      }
      database.updateData(thingToModify);
      this.thingsInSketchbook = database.getThings();


      // thingToModify.tag
    },
    saveEdit: function(e){
      e.preventDefault();
      var formElements = e.target.elements;
      var newThingData = this.editDetailsMenu.thing;
      var newThingTags = [];
      for (var i =0; i < formElements.length; i++){
        var el = formElements[i];
        if (el.id == "name"){
        
          newThingData.name = el.value;
        } else if (el.type == "checkbox"){
          if (el.checked) { newThingTags.push(el.name) }
        }
      }
      newThingData.tags = newThingTags;
      database.updateData(newThingData,this.editDetailsMenu.thing.id);
      this.editDetailsMenu.isOpen = false;    
    },
    cancelEdit: function(e){
      this.editDetailsMenu.isOpen = false;
      e.preventDefault();
    },
