    //For proper thing object representations
    thingIsSecret(thing){
      if (thing.tags.includes("secrets")){
        return true;
      } else {
        return false;
      }
    }
  },

  beforeDestroy(){
    window.removeEventListener('keydown',this.respondToKeyPress);
  },
