<script>
import $ from 'jquery';
import Popup from './Popup.vue';

export default {
  name: 'Bedroom',
  components:{
    Popup
  },
  watch:{
    //We are a menu on the screen. when we get "menu is false"
    // resetSelection: function(newVal){
      
    // }
    isMusicPlaying: function(newVal){
     this.playingMusic = newVal;
    }
  },
  data(){
    return{
      playingMusic: this.isMusicPlaying,
      displayBobPopup: false,
      displayDesktopPopup: false,
      displayPrinciplesPopup: false,
      displayThanks: false
    }
  },
  props: {
    msg: String,
    unreadMessage: Boolean,
    isMusicPlaying: Boolean
  },
  mounted(){
    let _this = this;
    window.addEventListener("keydown", function(e){
      _this.respondToKeyPress(e);
    });

    let star = $('.star')[0];
    let cloud = $(".cloud")[0];
    for (var i = 0; i < 200; i++){
      let starCopy = $(star).clone();
      let starCSS = {
        left: `${Math.random()*100}%`, 
        top: `${Math.random()*100}%`, 
        size: Math.random()*5
      };

      $(starCopy).css({
        'left': `${starCSS.left}`,
        'top': `${starCSS.top}`,
        'width': `${starCSS.size}px`,
        'height': `${starCSS.size}px`,
        });
      $(starCopy).appendTo("#starContainer");  


    }

    for (var j = 0; j < 10; j++){
      let cloudCopy = $(cloud).clone();
      let cloudCSS = {
        left: -900+Math.random()*1800, 
        top: Math.random()*window.innerHeight, 
        width: 100+Math.random()*100, 
        opacity: Math.random()*.5,
        duration:90+Math.random()*50
      };
     $(cloudCopy).css({
        'left':`${cloudCSS.left}px`,
        'top': `${cloudCSS.top}px`,
        'width': `${cloudCSS.width}px`,
        'opacity': `${cloudCSS.opacity}`,
        'animation-duration':`${cloudCSS.duration}s`
        });
      $(cloudCopy).appendTo("#starContainer");
    }
    //Create more animated music notes
    let musicNote =$($('.music-note')[0]);
    for (var k = 0; k < 3; k++){
      let note = $(musicNote).clone();
      $(note).css({
        'animation-delay': `${1+Math.random()*6}s`,
        'left': `${Math.random()*100}px`
      });
      $(note).appendTo(".music-animation");
    }
    // $('.bedroom').delay(2000).animate({opacity:1},2000);
  },
  methods:{
    // openApp(e){
    //   // console.log(e.target);
    //   var appId = e.target.getAttribute('value');
    //   this.$emit('messageFromBedroom',appId);
    // }
    toggleThanks(){
      this.displayThanks = !this.displayThanks;
    },
    openApp(appId){
      this.$emit('messageFromBedroom',appId);
    },
    toggleMusic:function(){
      this.playingMusic = !this.playingMusic;
      if (this.playingMusic == false){
        this.$root.$emit('stopMusicMessage')
      } else {
        this.$root.$emit('playMusicMessage')
      }
    },
    respondToKeyPress(e){
      // ` key
      console.log(e);
      //  if (e.which == 49){
      //   console.log("toggle principiles");
      //   console.log(this.displayPrinciplesPopup);
      //   this.displayPrinciplesPopup = !this.displayPrinciplesPopup;
      // }
      // if (e.which == 50){
      //   console.log("toggle desktop");
      //   console.log(this.displayDesktopPopup);
      //   this.displayDesktopPopup = !this.displayDesktopPopup;
      // }
      // if (e.which == 51){
      //   console.log("toggle bob");
      //   console.log(this.displayBobPopup);
      //   this.displayBobPopup = !this.displayBobPopup;
      // }
      //Caps lock
      // if (e.which == 16){
      //   console.log("bring up thanks screen");
      //   this.displayThanks = !this.displayThanks;
      // }
    },
    goToSleep(){
      //Logic for going to sleep, replace with sleep interaction
      this.displayThanks = true;
    }
  },
  beforeDestroy(){
    // window.removeEventListener('keydown',this.respondToKeyPress);
  }
}
</script>

