<p></p><template>
  <transition name="fade">
  <div v-if="show" class="navigation">
      <div class="buttons">
      <button class="goToBedroomLink" v-on:click="goToBedroom"></button>
      <button class="musicButton" v-on:click="toggleMusic"
      v-bind:class="{'muted':!isMusicPlaying}"
      ></button>
      <button class="guideButton" v-show="appId != 'panda-guide'" v-on:click="openGuide()">
      </button>
      </div>
      <div class="applicationTitle">
        <h1>{{currentAppName}}</h1> <!-- <button></button> -->
      </div>
  </div>
</transition> 
</template>

<script>
export default {
  name: 'Navigation',
  data(){
    return{
      show:true,
      playingMusic: this.isMusicPlaying
    }
  },
  props: {
    msg: String,
    currentAppName:String,
    appId:String,
    isMusicPlaying: Boolean,
    
  },
  methods:{
    goToBedroom:function(){
      console.log("goToBedroomMessage sending");
      this.$root.$emit('goToBedroomMessage')

    },
    openGuide:function(){
      this.$root.$emit('openGuideFromAppMessage',this.appId)
    },
    toggleMusic:function(){
      this.playingMusic = !this.playingMusic;
      if (this.playingMusic == false){
        this.$root.$emit('stopMusicMessage')
      } else {
        this.$root.$emit('playMusicMessage')
      }
    }
  }
}
</script>

