<template>
  <div class="sky-gradient" id="app" v-on:click="clickOnApp"  v-on:contextmenu="supressMenu" >
    <div class="loadingScreen">
      <div class="content">
        <h1>Jackie's OS</h1>
        <div class="loadingBar">
          <div class="loadingRainbow pastel-rainbow vertical"></div>
        </div>
      </div>
    </div>
    <transition name="fadeSimple">
    <div class="loginScreen" v-show="!appStarted">
        <div class="bg-cloud a"></div>
        <div class="bg-cloud b"></div>
        <div class="bg-cloud c" ></div>
        <div class="bg-cloud d" ></div>
        <div class="bg-cloud e" ></div>
        <div class="content">
        <h1>Welcome back, Jackie!</h1>
          <button v-on:click="startApp" class="bold">Enter My Bedroom</button>
        </div>
    </div>
   </transition>

    <transition name="bedroom">
    <Bedroom ref="bedroom" v-show="openApplication == null" v-on:messageFromBedroom="bedroomMessageReceived"
    v-bind:unreadMessage=unreadMessage 
    v-bind:isMusicPlaying=isMusicPlaying />
    </transition>

    <transition name="applicationFade" mode="out-in">
     <component v-bind:class="{behind:showGuideOverApplication}" id="openApplication" v-bind:is="openApplication" v-bind:applicationData=applicationData
    v-bind:isMusicPlaying=isMusicPlaying
     />
    </transition>

    <PandaGuide class="overlayingGuide" v-show="showGuideOverApplication" v-bind:isOverlaying=true v-bind:appId='overlayingGuideApp'/>
   <!--   <vue-p5 v-on="{preload:preload,setup:setup}"></vue-p5> -->
  </div>
 
</template>

<script>
import Bedroom from './components/Bedroom.vue'
import Things from './components/Things.vue'
import Sketchbook from './components/Sketchbook.vue'
import Explorer from './components/Explorer.vue'
import Messages from './components/Messages.vue'
import PandaGuide from './components/PandaGuide.vue'
import Timeteller from './components/Timeteller.vue'
// import Flute from './components/Flute.vue'
import Navigation from './components/Navigation.vue'
import Space from './components/Space.vue'
import * as database from './database.js';
import * as Pizzicato from 'pizzicato';
import * as $ from 'jquery';

import { page } from 'vue-analytics'






document.addEventListener('fullscreenchange', () => {
  if (document.fullscreenElement) {
    console.log("App is fullscreen")
  } else {
   console.log("Fullscreen is off")
  }
});



export default {
  name: 'App',
  components: {
    Bedroom,
    Things,
    Sketchbook,
    Navigation,
    Space,
    Explorer,
    Messages,
    PandaGuide,
    Timeteller
    // ,
    // Flute
  },
  data(){
    let songFilter = new Pizzicato.Effects.LowPassFilter({
        frequency:400
        });
    return{
      openApplication: null,
      supressMenus: false,
      applicationData: {},
      appStarted: false,
      mySound: null,
      themeSong: null,
      showGuideOverApplication: false,
      overlayingGuideApp: "",
      unreadMessage: true,
      songFilter: songFilter,
      isMusicPlaying: true
    }
  },
  mounted(){

    //Startup animation
    let animationSpeeds = {
      loadingBar:5000,
      loadingScreen:1000,
      timeoutLoading:2000,
      loginFade:1000,
      timeoutLogin:3000,
    };
    // let animationSpeeds = {
    //   loadingBar:1,
    //   loadingScreen:1,
    //   timeoutLoading:1,
    //   loginFade:1,
    //   timeoutLogin:1,
    // };
    let bedroomEl = this.$refs.bedroom.$el;
    $(bedroomEl).hide();
    $('.loginScreen').hide();
    $('.loadingRainbow').animate({width:"100%"},animationSpeeds.loadingBar, function(){
       $('.loadingScreen h1').addClass("glowText");
       $('.loadingBar').addClass("glowAnimation");
       setTimeout(function(){
        $('.loadingScreen').fadeOut(animationSpeeds.loadingScreen);
      },animationSpeeds.timeoutLoading);
       setTimeout(function(){
        $('.loginScreen').fadeIn(animationSpeeds.loginFade);
      },animationSpeeds.timeoutLogin);
    });
    $('.bg-cloud').each(function(){
      let randomDelay = -Math.random()*4;
      $(this).css("animation-delay",`${randomDelay}s`);
    })


    
    // Event listeners
    this.$root.$on('goToBedroomMessage', ()=> {
      this.themeSong.removeEffect(this.songFilter);
      this.openApplication = null;

      let volume = this.themeSong.volume;
      let _this = this;
      function increaseVolume(){
        _this.themeSong.volume = volume;
        volume = volume + 0.01;
        if (volume > 0.4){
           clearInterval(increaseMusic);
        }
      }
      let increaseMusic = setInterval(increaseVolume,15);
    
    });

    this.$root.$on('switchApplicationMessage', (appToOpen)=> {
      this.switchApplication(appToOpen);
    
    });

    this.$root.$on('playMusicMessage', ()=> {
      this.playMusic();
      this.isMusicPlaying = true;
    });

    this.$root.$on('stopMusicMessage', ()=> {
      this.stopMusic();
      this.isMusicPlaying = false;
    });

    this.$root.$on('openGuideFromAppMessage', (appId)=> {
      // this.stopMusic();
      console.log("opening guide with appId"+appId);
      this.showGuideOverApplication = true;
      this.overlayingGuideApp =appId;
    });
     this.$root.$on('closeOverlayingGuideMessage', ()=> {
      // this.stopMusic();
      this.showGuideOverApplication = false;
      this.overlayingGuideApp ="";
    });

    this.$root.$on('removePostcardNotificationMessage', ()=> {
      this.unreadMessage = false;
    });


    database.openDatabase();

    this.track();

    
  },

  methods:{

   track () {
      page('/')
    },

        /* View in fullscreen */
    openFullscreen: function(){
      var elem = document.documentElement;
      if (elem.requestFullscreen) {
        elem.requestFullscreen();
      } else if (elem.mozRequestFullScreen) { /* Firefox */
        elem.mozRequestFullScreen();
      } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
        elem.webkitRequestFullscreen();
      } else if (elem.msRequestFullscreen) { /* IE/Edge */
        elem.msRequestFullscreen();
      }
    },

    supressMenu: function(e){
      console.log("supress right click menu:");
      if (this.supressMenus){e.preventDefault();}
    },
    clickOnApp: function(){
      // this.startApp();
      // this.removeMenus();
    },

    startApp: function(){
      if(!this.appStarted){
        let _this = this;
        setTimeout(function(){
          console.log("starting app");
          // _this.openFullscreen();
          _this.themeSong = new Pizzicato.Sound({
            source: 'file',
            options:{path:'./sound/themesong.m4a',loop:true}
          }, function() {
          // Sound loaded!
          // _this.themeSong.play();

          // var lowPassFilter = new Pizzicato.Effects.LowPassFilter({
          // volume:.2
          // });

          // _this.themeSong.addEffect(lowPassFilter);
          _this.themeSong.volume = 0.4;
          _this.themeSong.play();
          let bedroomEl = _this.$refs.bedroom.$el;
          $(bedroomEl).fadeIn(2000);

  
          });          
        },500);

      }
      this.appStarted =true;
      
    },
    bedroomMessageReceived(appToOpen){
      console.log('bedroom message', appToOpen);
      let volume = 0.4;
      let _this = this;
      function decreaseMusic(){
        _this.themeSong.volume = volume;
        volume = volume - 0.01;
        if (volume < 0.15){
           clearInterval(lowerMusic);
        }
      }
      let lowerMusic = setInterval(decreaseMusic,15);


      switch( appToOpen){
        case "things":
          this.openApplication = Things;
          break;
        case "sketchbook":
          this.applicationData = {thingId:1, editingMode : false}
          this.openApplication = Sketchbook;
          break;
        case "explorer":
          this.openApplication = Explorer;
          break;
        case "messages":
          this.openApplication = Messages;
          break;
        case "panda-guide":
          this.openApplication = PandaGuide;
          break;
        case "timeteller":
          this.openApplication = Timeteller;
          break;
        // case "flute":
        //   this.openApplication = Flute;
        //   break;
        default:
          this.themeSong.removeEffect(this.songFilter);
          this.openApplication = null;
      }

    },
    switchApplication(data){
      console.log("Switch application message received");
      switch(data.destination){
        case "things":
          this.openApplication = Things;
          break;
        case "sketchbook":
          this.openApplication = Sketchbook;
          if (data.thingId != null) {
            this.applicationData = {thingId: data.thingId, editingMode:true};
          } else{
            this.applicationData = {thingId:1, editingMode : false}
          }
          break;
        default:
          this.openApplication = null;
      }      
    }
    ,
    stopMusic(){
      console.log("STOP MUSIC MESSAGE RECEIVED");
      this.themeSong.pause();
    },
     playMusic(){
      console.log("PLAY MUSIC MESSAGE RECEIVED");
      this.themeSong.play();
    }
    // preload(){
    //   VueP5.soundFormats('m4a');
    //   this.mySound = VueP5.loadSound('/sound/themesong.m4a');

    // },
    // setup(){
    //   this.mySound.play();
    // }
    // removeMenus: function(){
    //   console.log("removing all menus");

    // }

  }
}
</script>

