<template>
  <div class="explorer" v-bind:class="{'duringEditing':currentlyEditing,'showingSidebar': showSidebar}" >
    <Navigation currentAppName="Cloud Explorer" appId="explorer"
    v-bind:isMusicPlaying=isMusicPlaying />


    <div id="explorer-nav" v-bind:class="{'editing':currentlyEditing}">
      <input v-show=!currentlyEditing type="text" id="searchbar" v-bind:value=searchbarURL>

      <h3 class="previewingMessage" v-show=currentlyEditing>Previewing changes to jackies.world</h3>

      <div v-show="!currentlyEditing" id="home-and-faves">
        <button v-on:click="goHome">&#x1F3E0;</button>
        <button v-on:click="toggleFaves" class="group">Faves</button>
      </div>


    </div>

    <div class="iframeContainer">
    <iframe 
    id="iframe" 
  v-bind:src=siteURL
  srcdoc=""
     >
     </iframe>
     <button class='editSiteButton' v-if=homepage v-show="!currentlyEditing" v-on:click="editSite">Edit site</button>
     <button class='editSiteButton' v-if=homepage v-show="currentlyEditing" v-on:click="saveChanges">Finish Editing</button>
   </div>
   
    <div class="sidebar" v-show="showSidebar" v-bind:class="{'editing':currentlyEditing,'visible':showSidebar}">
      
      <div v-show=currentlyEditing class="editingSite">
        <div class="codingArea" ref="codingArea"></div>
      </div>
     

        <div v-show=(!currentlyEditing) class="faves">
          <!-- <a v-on:click="closeFaves">Close</a> -->
          <h2>My Faves</h2>
          <ul>
            <li v-on:click="clickFaveLink"
            displaysrc="http://search.deviantart.com/?section=browse&qh=boost%3Apopular+age_sigma%3A24h+age_scale%3A5&q=Naruto"
            src="https://web.archive.org/web/20070611183513if_/http://search.deviantart.com/?section=browse&qh=boost%3Apopular+age_sigma%3A24h+age_scale%3A5&q=Naruto">naruto artwork</li>
            <li v-on:click="clickFaveLink"
            displaysrc="http://www.musogato.com/avatar/"
            src="https://web.archive.org/web/20060702223502if_/http://www.musogato.com/avatar/">cool avatar fansite</li>
            <li v-on:click="clickFaveLink" 
            displaysrc="http://en.wikipedia.org/wiki/Easy-Bake_Oven"
            src="https://web.archive.org/web/20071112014413if_/http://en.wikipedia.org/wiki/Easy-Bake_Oven">easy back oven</li>
            <li v-on:click="clickFaveLink"
            displaysrc="http://yahooligans.yahoo.com/"
            src="https://web.archive.org/web/20060610124851if_/http://yahooligans.yahoo.com/">yahooligans</li>
            <li v-on:click="clickFaveLink"
            displaysrc="http://www.oekakiart.com/dhpractice/"
            src="https://web.archive.org/web/20070526024402if_/http://www.oekakiart.com/dhpractice/">avatar drawing board</li>
          </ul>
        </div>

    </div>
   
  </div>
</template>

<script>
import Navigation from './Navigation.vue';
import CodeMirror from 'codemirror';
import 'codemirror/lib/codemirror.css';
import 'codemirror/theme/panda-syntax.css';
import 'codemirror/mode/htmlmixed/htmlmixed.js';
import  $ from 'jquery';

export default {
  name: 'Explorer',
  components: {
    Navigation
  },
  watch:{
    //We are a menu on the screen. when we get "menu is false"
    menu: function(newVal){
        this.showMenu = (newVal == true);
    }

  },
  data(){

     let siteHTML = `<!DOCTYPE html>
      <html>
      <head>
      <title>Jackie's World</title>
      <style>
        body{
          text-align:center;
          font-family: "Jackie Comic";
          background-color: #e1fdff;
        }
        img{
          max-width:300px;
          max-height:300px;
          display:inline;
        }
        h1{
          font-size:40px;
        }
        #marquee{
          width:100%;
          padding:20px;
          background: linear-gradient(90deg, rgba(255,185,255,1) 0%, rgba(255,188,146,1) 14%, rgba(236,255,162,1) 30%, rgba(186,252,199,1) 47%, rgba(162,248,255,1) 64%, rgba(212,203,255,1) 83%, rgba(255,185,255,1) 100%);
        }
      </style>
      </head>
      <body>

      <h1 style="color:#FF00FF;"">Jackie's World</h1>
      <img src="/img/panda-icon.gif"><br>
      <marquee id="marquee"> Hi, welcome to Jackie's World! My name is Jackie, thank you for coming to my online website!!! </marquee>
      
      <h2>About me</h2>
      <p>I am a Chinese-American girl who loves <b>drawing</b> and watching my favorite shows and talking with my friends on the phone. My fave color is
      <b>
      <span style="color:red">R</span><span style="color:orange">A</span><span style="color:yellow">I</span><span style="color:green">N</span><span style="color:blue">B</span><span style="color:purple">O</span><span style="color:violet">W</span>
      </b>!!</p>
      <img src="/img/ID2.png"><img src="/img/chinese-name.png">
      <h2> My art</h2>
      <img src="/img/chibi-yue.jpg">
      <img src="/img/hinatasnow.png">
      <img src="/img/i-will-always-be-with-you.png">
      <img src="/img/blossom.png">
      <img src="/img/iSakura.png">
      <h2> My favorite foods</h2>
      <p> I love to eat barbecue <b>ribs</b>. I love any food with chilis in it, including chili! the Spicier the Better!</p>
      <h2> My interests</h2>
      <p>I LOOOOOOOOOOOVE <b>Avatar</b>: The Last Airbender!! Naruto is pretty cool too. My fave movie is Kiki's Delivery Service. ^_^ Pandas are my favorite animal.</p>
      <img src="/img/me-in-avatar-world.png">
      <p>Here is me if I lived in the Avatar world. I would definitely be a water bender!</p>
      <h2>Here's my home address!</h2>
      <p> 24 Oak Dr....<br>
      HA! You thought I would give out my personal information?<br>
      I fooled you, you creepy stalker!!!!</p>

      </body>
      </html>`;
   
   let errorHTML =  `<!DOCTYPE html>
      <html>
      <head>
      <title>Jackie's World</title>
      <style>
        h1{
          padding-top:20px;
          font-size:42px;
        }
        img{
          width:200px;
        }
        body{
          text-align:center;
          font-family:"Jackie Comic";
        }
        img{
          max-width:100%;
        }
      </style>
      </head>
      <body>

      <h1>Uh oh!! Oopsie poopsie!</h1>
      <img src="/UI/rainbow-poop.png">
      <p>Something ended up funky... in the meantime, check out your homepage or browse your faves.</p>

      </body>
      </html>`
    console.log(localStorage.getItem('siteHTML'));
    console.log(errorHTML);
    if (localStorage.getItem('siteHTML') == null){
       localStorage.setItem("siteHTML",siteHTML);
    } else {
      siteHTML  = localStorage.getItem('siteHTML');
    }
    // let siteURL = "https://en.wikipedia.org"

    return {
      siteURL: "https://en.wikipedia.org",
      searchbarURL: "http://jackies.world",
      homepage:true,
      showSidebar: false,
      currentlyEditing: false,
      siteHTML: siteHTML,
      codeMirror: null,
      errorHTML: errorHTML
    }
    
  },
  computed:{

   
  },
  mounted(){
    let _this = this;
    console.log(CodeMirror);
    document.getElementById("iframe").setAttribute("srcdoc",this.siteHTML);
    let codingArea = this.$refs.codingArea;
    this.codeMirror = CodeMirror(codingArea,{
      value: this.siteHTML,
      mode: "htmlmixed",
      theme: 'panda-syntax'
    });

    $('.goToBedroomLink').click(()=>{

    })

    document.getElementById('searchbar').addEventListener('keydown',function(e){
      // e.preventDefault();
      if (e.keyCode==13){
        //submit the text, attempt to go to that thing
        console.log(e.target.value);
        var attemptedURL = e.target.value;
        _this.searchbarURL = attemptedURL;
        if (_this.searchbarURL == "jackies.world" || _this.searchbarURL =="www.jackies.world" || _this.searchbarURL == "http://jackies.world" || _this.searchbarURL == "http://www.jackies.world"){
          console.log("go to jackies world");
          document.getElementById("iframe").setAttribute("srcdoc",_this.siteHTML);
        } else {
          
          document.getElementById("iframe").setAttribute("srcdoc",_this.errorHTML);
        }

  
      }
    })
  },
  methods:{
    editSite: function(){
      this.showSidebar = true;
      this.currentlyEditing = true;
      var _this = this;
      setTimeout(function(){
        _this.codeMirror.refresh()
      });

      this.codeMirror.on("change",function(cm){
        console.log("code mirror changed");
        var newValue = cm.getValue()
        _this.siteHTML = newValue;
        document.getElementById("iframe").setAttribute("srcdoc",_this.siteHTML);
      })
    },
    saveChanges: function(){
      let newHTML = this.codeMirror.getValue();
      localStorage.setItem("siteHTML",newHTML);
      this.showSidebar = false;
      this.currentlyEditing = false;

    },
    cancelEditing: function(){
      let previousHTML = localStorage.getItem("siteHTML");
      document.getElementById("iframe").setAttribute("srcdoc",previousHTML);
      this.showSidebar = false;
      this.currentlyEditing = false;
    },
    goHome: function(){
      this.searchbarURL = "http://jackies.world";
       document.getElementById("iframe").setAttribute("srcdoc",this.siteHTML);
       this.homepage = true;
    },
    toggleFaves: function(){
      this.showSidebar = !this.showSidebar;
      this.currentlyEditing = false;
       // document.getElementById("iframe").removeAttribute("srcdoc");
    },
    closeFaves:function(){
      this.showSidebar = false;
    },
    clickFaveLink:function(e){
      // let src = e.target.src;
      console.log(e.target.getAttribute('src'));
      this.siteURL = e.target.getAttribute('src');
      this.searchbarURL = e.target.getAttribute('displaysrc');
      document.getElementById("iframe").removeAttribute("srcdoc");
      this.homepage = false;
    }
  },
  props: {
    name: String,
    data: Object,
    menu: Boolean,
    isMusicPlaying: Boolean
  }
}
</script>

