<template>
  <div class="secretAuthentication" v-bind:class="{'correct':(answered && answeredCorrectly),'incorrect':(answered && !answeredCorrectly)}">
    <div class="lightsContainer">
      <div class="light"></div>
    </div>
    <div class="prompt">
    
      <transition name="fade" appear>
        <div class="text text-response" v-if=answered >
          <h1 id="response" v-html=response></h1>
        </div>
        <div class="text" v-else>
          <h3>ANSWER THE QUESTION TO PROVE YOU'RE ME</h3>
          <h1>{{question}}</h1>
        </div>
      </transition>
      <input v-bind:class="{'correct':(answered && answeredCorrectly),'incorrect':(answered && !answeredCorrectly)}"
      type="text" v-on:keyup=submitAnswer placeholder="Answer carefully...">
     
    </div>
  </div>
</template>

<script>
import * as $ from 'jquery';
import * as Pizzicato from 'pizzicato';

export default {
  name: 'SecretAuthentication',
  components: {
  },
  data(){
    const questionAndAnswers = [
    {question:"What is my favorite color?",answer:"rainbow"},
    {question:"What is my all-time favorite TV show?",answer:"avatar"},
    {question:"What's my favorite saucy food?",answer:"ribs"},
    ];
    const chosenPair = questionAndAnswers[Math.floor(Math.random()*questionAndAnswers.length)];
    const question = chosenPair.question;
    const correctAnswer = chosenPair.answer;
    const response = "Wrong choice, poopyhead!!!<br>Get away from my computer!!"
    let setInterval = window.setInterval(function(){
      $(".light").toggleClass('on');
       $(".light").toggleClass('off');
    },1000);

 
    return{
      question:question,
      correctAnswer:correctAnswer,
      response:response,
      answered:false,
      answeredCorrectly:false,
      setInterval: setInterval,
      correctSound: null,
      incorrectSound: null,
      anticipationSound: null
    }
  },
  mounted(){

    let _this = this;
   _this.correctSound = new Pizzicato.Sound({
        source: 'file',
        options:{path:'./sound/correctNotification.m4a',loop:false}
      }, function(){
        _this.correctSound.volume = .3;
        
    });
    
    _this.incorrectSound = new Pizzicato.Sound({
        source: 'file',
        options:{path:'./sound/incorrectNotification.m4a',loop:false}
      }, function(){
        _this.incorrectSound.volume = .3;

    });
    
    _this.anticipationSound = new Pizzicato.Sound({
        source: 'file',
        options:{path:'./sound/anticipation.m4a',loop:true}
      },function(){
        _this.anticipationSound.volume = .1;
        _this.anticipationSound.play();
    }); 

    let lightsContainer = $('.lightsContainer');
    let lightEl = $('.light')[0];

    const horizontalSubdivisions = 14;
    let percentLeft = 0;
    let pixelLeft =0;
    //Make bottom and top lights
    for (var i = 0; i < horizontalSubdivisions + 1; i++){
      let state = "";
      if (i % 2 == 0){
        state = "on";
      } else {
        state = 'off';
      }
      let lightCopyBottom= $(lightEl).clone();
      $(lightCopyBottom).css('left', `calc(${percentLeft}% - ${pixelLeft}px`);
      $(lightCopyBottom).addClass(`bottom ${state}`);
       $(lightsContainer).append(lightCopyBottom);
      let lightCopyTop = $(lightEl).clone();
      $(lightCopyTop).css('left', `calc(${percentLeft}% - ${pixelLeft}px`);
      $(lightCopyTop).addClass(`top ${state}`);
      $(lightsContainer).append(lightCopyTop);
      percentLeft += 100/horizontalSubdivisions;
      pixelLeft += 40/horizontalSubdivisions;
    }
    //Make side lights
    const verticalSubdivions = 8;
    let percentTop = 100/verticalSubdivions;
    let pixelTop = 0;
    for (var j = 0; j < verticalSubdivions - 1; j++){
      let state = "";
      if (j % 2 == 0){
        state = "off";
      } else {
        state = 'on';
      }
      pixelTop += 40/verticalSubdivions;
      let lightCopyBottom= $(lightEl).clone();
      $(lightCopyBottom).css('top', `calc(${percentTop}% - ${pixelTop}px)`);
      $(lightCopyBottom).addClass(`left ${state}`);
       $(lightsContainer).append(lightCopyBottom);
      let lightCopyTop = $(lightEl).clone();
      $(lightCopyTop).css('top', `calc(${percentTop}% - ${pixelTop}px)`);
      $(lightCopyTop).addClass(`right ${state}`);
      $(lightsContainer).append(lightCopyTop);
      percentTop += 100/verticalSubdivions;
      
    }

    
  },
  methods:{
    submitAnswer(e){
      if (e.keyCode === 13 && e.target.value !== null) {
        this.answered = true;
        let submittedAnswer = e.target.value;
        let _this = this;
        window.clearInterval(this.setInterval);
        $('.light').removeClass('off').addClass('on');
        if (submittedAnswer.toLowerCase() == this.correctAnswer){
          
          _this.answeredCorrectly = true;
          _this.response = "Yayyy!!! You know me so well ^_^"
          //Play sound
          console.log(_this.correctSound)
          _this.correctSound.play();
          _this.anticipationSound.stop();
          setTimeout(function(){

            _this.$emit('authenticationSuccess');
          },3000)
         
        } else {
          $('.light').addClass('incorrect');
          _this.answeredCorrectly = false;
          //Play sound
          console.log(_this.incorrectSound)
           _this.incorrectSound.play();
          _this.anticipationSound.stop();
          setTimeout(function(){ 
            
            _this.$emit('authenticationFail');
          },3000)
          
        }
      }
    }
  },
  beforeDestroy(){
    window.clearInterval(this.setInterval);
    this.anticipationSound.stop();
    this.incorrectSound.stop();
    this.correctSound.stop();
  },
  props: {

  }
}
</script>

