export let tree = Arboreal.parse(thingsCategories, 'subspaces');

console.log(tree);




//Helper function that stores all tags
var childTags = [];
function iterator (node) {
  childTags.push(node.data.name);
}
tree.traverseDown(iterator);

export const allTags = ()=> {
  return childTags;
}




