// addData:
//  adds data to the global db variable
export let addData = (name,src,tags,canvasData,date) => {
  // console.log("running ADD DATA");
  // grab the values entered into the form fields and store them in an object ready for being inserted into the DB
  let newItem = { name:name,src:src,tags:tags,canvasData:canvasData,date:date};

  // open a read/write db transaction, ready for adding the data
  let transaction = db.transaction(['things_os'], 'readwrite');

  // call an object store that's already been added to the database
  let objectStore = transaction.objectStore('things_os');

  // Make a request to add our newItem object to the object store
  let request = objectStore.add(newItem);
  request.onsuccess = function() {
      let num = localStorage.getItem('numOfThings');
      if (num ==null){
        localStorage.setItem('numOfThings','1');
      } else{
        let numInt = parseInt(num);
        localStorage.setItem('numOfThings',numInt+1);
      }
      // console.log("add data success");
      let getAllRequest = objectStore.getAll();
      getAllRequest.onsuccess = function(){
        // console.log(getAllRequest.result);
        return (getAllRequest.result);
      }
  };

  // Report on the success of the transaction completing, when everything is done
  transaction.oncomplete = function() {

    // console.log('Transaction completed: database modification finished.');
    // console.log("added item with"+"name: "+name+ " src: "+src+" tags: "+tags );
  };

  transaction.onerror = function() {
    // console.log('Transaction not opened due to error');
  };
}

