// getThings:
//  gets things that have a specific tag. or a list of tags if tag is empty, returns all things.
// tagName is list of tags: [], ["tagname"], ["tagname1","tagname2"]
//options is object with tagname, limit, and callback (all optional)
export let getThings = (options) => {
  let tags = []; let limit = 99999999; let callback = null;
  if (options == null){
    tags = []
  } else {

    if (options.tags == null){
      tags = []
    } else if (Array.isArray(options.tags)) {
      tags = options.tags
    } else if (typeof options.tags == "string") {
      tags = [options.tags]
    }
    if (options.limit !== null && options.limit < limit){
      limit = options.limit;
    }
    if (options.callback !== null && (typeof options.callback == "function")){
      callback = options.callback;
    }
  } 
  let thingsToReturn = [];
  const transaction = db.transaction(['things_os'], 'readonly');
  const thingsStore = transaction.objectStore('things_os');
  const getCursorRequest = thingsStore.openCursor();
  let count = 0;
    getCursorRequest.onsuccess = e => {
        const cursor = e.target.result;
        if (cursor) {
            if (tags.length > 0){
              for (var i = 0; i < tags.length;i++){
                var name = tags[i];
                if (cursor.value.tags.includes(name)&& !(thingsToReturn.includes(cursor.value))){
                  count++;
                  thingsToReturn.push(cursor.value);
                }
              }
            // Else we are trying to get every tag
            } else {
              if (!(thingsToReturn.includes(cursor.value))){
                  count++;
                  thingsToReturn.push(cursor.value);
                }
            }
            if ((options != null && limit>count) || options == null){
              cursor.continue();
            }
        } 
    }
   transaction.oncomplete = function(){
    if (callback !== null){
        callback();
      }
    }
  return thingsToReturn;
}

