// openDatabase:
//    Opens the things_db database.
//    If the database is not created already, this creates it as well as
//    fills the database with the existing things from the json.
export let openDatabase = function(){ // eslint-disable-line no-unused-vars
  console.log('openDatabase');
  // window.onload=function() {
  let request = window.indexedDB.open('things_db',1);
  // onerror handler signifies that the database didn't open successfully
  request.onerror = function() {
    console.log('Database failed to open');
  };

  // onsuccess handler signifies that the database opened successfully
  request.onsuccess = function() {
    console.log('Database opened successfully');
    // Store the opened database object in the db variable. This is used a lot below
    db = request.result;
    console.log(db);
    // Add initial things to database if not done before
    if (localStorage.getItem('appPopulated') == null){
      localStorage.setItem('appPopulated',true);
      var initialThings = initialThingsJSON.initialThings;
      initialThings.sort((b,a)=> new Date(`${b.date.year}-${b.date.month}-${b.date.day}T00:00:00`)
        - new Date(`${a.date.year}-${a.date.month}-${a.date.day}T00:00:00`)
        );
      console.log("initial things after sort:")
      console.log(initialThings);
      initialThings.map(data=>{
        let canvasData = "";
        if (data.canvasData != null){
          canvasData = data.canvasData;
        }
        addData(data.name,data.src,data.tags,canvasData,data.date);
      })
      var initialPostcards = initialPostcardsJSON.initialPostcards;
      initialPostcards.map(data=>{
        addData_Postcard(data.toData,data.fromData,data.canvasData,data.imgSrc,data.message,data.unread);
      })
    }

  };


  // Setup the database tables if this has not already been done
  request.onupgradeneeded = function(e) {
    // Grab a reference to the opened database
    let db = e.target.result;

    // Create an objectStore to store our notes in (basically like a single table)
    // including a auto-incrementing key
    let objectStore = db.createObjectStore('things_os', { keyPath: 'id', autoIncrement:true });

    // Define what data items the objectStore will contain
    //Name of file: e.g. Cool Pic of me
    objectStore.createIndex('name', 'name', { unique: false });
    //Src of file: either dataURL or location in the assets
    objectStore.createIndex('src', 'src', { unique: false });
    //Tags: e.g. ["sadness","dreams"]
    objectStore.createIndex('tags', 'tags', { unique: false , multientry:true});
    objectStore.createIndex('canvasData', 'canvasData', { unique: false});
    objectStore.createIndex('date', 'date', { unique: false});
    // //Ancestor tags: e.g. ["emotions","sad"] for "forlorn"
    // objectStore.createIndex('ancestorTags', 'ancestorTags', { unique: false , multientry:true});



    let postcardStore = db.createObjectStore('postcards_os', { keyPath: 'id', autoIncrement:true });
    postcardStore.createIndex('toData', 'toData', { unique: false });
    postcardStore.createIndex('fromData', 'fromData', { unique: false });
    postcardStore.createIndex('message', 'message', { unique: false});
    postcardStore.createIndex('canvasData', 'canvasData', { unique: false});
    postcardStore.createIndex('imgSrc', 'imgSrc', { unique: false});
    postcardStore.createIndex('unread', 'unread', { unique: false});

    console.log('Database setup complete');

  };
}

