<template>
  <div ref="postcard" class="postcard"
  v-on:mousedown="mousedown"
  v-on:click="selectPostcard"
  v-bind:class="{'unread':unread}"
  >
  <div class="newBubble" v-show="unread">New!</div>
    <div ref="postcardFront" class="postcard-front frontVisible">
       <img v-bind:src=imgSrc>
    </div>
    <div ref="postcardBack" class="postcard-back" v-bind:class="{'isSelected':isSelected}">
      <div class="leftSide">
        <div class="message-container">
          <pre v-html="message"></pre>
        </div>
      </div>
      <div class="rightSide">
        <div class="stamp"></div>
        <p class="postmarked">written: {{fromData.date}}</p>
         <p><span class="label">FROM</span><br> {{fromData.name}}</p>
        <p><span class="label">TO</span><br>{{toData.name}}<br></p>
      </div>
    </div>
    <div class="options" v-show="isSelected" >
      <button v-if="flippedToBack" v-on:click="flip">
      Flip Postcard
      </button>
      <button v-else v-on:click="flip">
      Read postcard
      </button>
    </div>
  </div>
</template>

<script>
// import Navigation from './Navigation.vue';
import  $ from 'jquery';
// import * as database from '../database.js';


export default {
  name: 'Postcard',
  components: {
    
  },
  watch:{
    //We are a menu on the screen. when we get "menu is false"
    reset: function(newVal){
      console.log(newVal);
          if (newVal == true){
            this.flippedToBack = true;
            console.log("RESETTING POSTCARD");
              this.isSelected = false;
          } 
        }

  },
  data(){
    return {
      flippedToBack: false,
      isCurrentlyDragging: false,
      clickIsValid: true,
      isSelected: false
    }
    
  },
  computed:{

   
  },
  mounted(){
    let postcardEl = this.$refs.postcard;
    this.postcardEl = postcardEl;
    let stopReadingButton = document.getElementById("stopReadingButton");
    let _this = this;
    stopReadingButton.addEventListener("click",function(){
      _this.isSelected = false;
    })
    window.setTimeout(function(){
      $(postcardEl).addClass('displayed').removeClass('notLoaded');
    },400)
  },
  methods:{
   flip(){
    if (this.unread){
      $(".newBubble").fadeOut();
      $(".postcard-front,.postcard-back").css("box-shadow",'1px 13px 20px rgba(0,0,0,.08)');
      this.$emit("madePostcardUnreadMessage", this.postcardData);
    }
    //We are currently showing the front, and need to flip it to the back
    if (!this.flippedToBack){
      this.$refs.postcardFront.classList.remove("flipping-from-back");
      this.$refs.postcardBack.classList.remove("flipping-from-back");
      this.$refs.postcardFront.classList.add("flipping-from-front");
      this.$refs.postcardBack.classList.add("flipping-from-front"); 
      this.flippedToBack = true;
    //Currently showing the back, need to flip it to the front
    } else {
      this.$refs.postcardFront.classList.remove("flipping-from-front");
      this.$refs.postcardBack.classList.remove("flipping-from-front");
      this.$refs.postcardFront.classList.add("flipping-from-back");
      this.$refs.postcardBack.classList.add("flipping-from-back");
      this.flippedToBack = false;
    }

   },
   selectPostcard(e){
    if (this.clickIsValid == true){
      this.isSelected = true;
      this.$emit("readPostcardMessage", $(e.target).closest('.postcard-element')[0]);
    }
   },
   mousedown (){
    var _this = this;
    this.clickIsValid = true;
    setTimeout(function(){
      _this.clickIsValid = false;
    },200)
   }
   
  },
  props: {
    postcardData: Object,
    fromData: Object,
    toData: Object,
    message: String,
    imgSrc: String,
    canvasData: String,
    unread: Boolean,
    reset: Boolean

  }
}
</script>

