 /* .postcard.preview{
    transform:scale(.5,.5);
    position:relative;
  }*/

  img{
    width:100%;
    max-width:100%;
    border-radius:20px;
    /*height:auto;*/
    height:100%;
    max-height:100%;
  }

  .frontVisible:not(.flipping-from-back){
    z-index:2;
  }


.flipping-from-front, .flipping-from-back{
  
  animation-timing-function:linear;
  animation-duration:1.3s;
  animation-iteration-count:1;
  animation-fill-mode: forwards;  
}

.postcard-front.flipping-from-front{
  animation-name: flip-front;
}
.postcard-back.flipping-from-front{
  animation-name: flip-back;
}

.postcard-front.flipping-from-back{
  animation-name: flip-back-front;
}
.postcard-back.flipping-from-back{
  animation-name: flip-back-back;
}

@keyframes flip-front {
  0% {
    transform: perspective(0cm) rotateY(0deg); opacity:1;
  }
  50% {
    transform: perspective(65cm) rotateY(-90deg) ;
    opacity:1;
  }
  50.0001%{
    opacity:0;
  }
  100% {
    transform: perspective(0cm) rotateY(-180deg) ;
    opacity:0;
  }
}

@keyframes flip-back {
  0% {
    transform: perspective(0cm) rotateY(-180deg); opacity:0;
  }
  50% {
    transform: perspective(65cm) rotateY(-90deg) ;
    opacity:0;
  }
  50.0001%{
    opacity:1  rotateY(-90deg) ;
  }
  100% {
    transform: perspective(0cm) rotateY(0deg) ;
    opacity:1;
  }
}


@keyframes flip-back-front {

  0% {
    transform: perspective(0cm)  rotateY(-180deg);
    opacity:0;
  }
  50% {
    transform: perspective(65cm) rotateY(-90deg) ;
    opacity:0;
  }
  50.0001%{
     transform: perspective(65cm) rotateY(-90deg) ;
     opacity:1;
  }
  100% {
    transform: perspective(0cm) rotateY(0deg) ;
    opacity:1;
  }
}

@keyframes flip-back-back {
  0% {
    transform: perspective(0cm) rotateY(0deg); opacity:1;
  }
  50% {
    transform:  perspective(65cm) rotateY(-90deg) ;
    opacity:1;
  }
  50.0001%{
    opacity:0;
  }
  100% {
    transform:  perspective(0cm) rotateY(-180deg) ;
    opacity:0;
  }
}
