<template>
  <div ref="newPostcard" class="newPostcard" v-on:click="selectPostcard"
  v-bind:class="{'preview':!selectedPostcard,'selected':selectedPostcard}">
    <div v-show="showOptions" class="options">
     
      <span>{{design.name}}</span>&nbsp;&nbsp;&nbsp;
      <button class="flipButton" v-on:click="flip(false)">Flip postcard</button>
<!--       <button>change theme</button> -->
    </div>
 
    <div ref="postcardFront" class="postcard-front frontVisible">
       <img v-bind:src=design.src>
    </div>
    <div ref="postcardBack" class="postcard-back">
       <!-- <h1>This is a postcard</h1> -->
      <div class="leftSide">
        <textarea ref="messageInput" v-on:keyup="typeMessage" placeholder="Write your message here..." v-bind:value=message></textarea>
      </div>
      <div class="rightSide">
        <div class="stamp"></div>
        <!-- <p class="postmarked">written: {{fromData.date}}</p> -->
         <p><span class="label">FROM</span><br> {{fromData.name}}</p>
        <p><span class="label">TO</span><br>{{toData.name}}<br></p>
      </div>
      

    </div>
