<script>
// import Navigation from './Navigation.vue';
import  $ from 'jquery';
console.log($);

export default {
  name: 'NewPostcard',
  components: {
    
  },
  watch:{
    //We are a menu on the screen. when we get "menu is false"
    // resetSelection: function(newVal){
      
    // }
    reset: function(newVal){
      if (newVal == true){
        console.log("RESETTING POSTCARD");
        this.selectedPostcard=false;
        this.showOptions = false;
        if (this.flippedToBack){
          this.flip();
        }
        
      }
    }
  },
  data(){
    return {
      flippedToBack: false,
      selectedPostcard: false,
      showOptions: false
    }
    
  },
  computed:{

   
  },
  mounted(){
    console.log("DOING MOUNTED");
    // let postcardEl = this.$refs.postcard;
    // let postcardSpace = postcardEl.parentElement.parentElement;
    // let spaceDimensions = {width:parseInt($(postcardSpace).css('width'),10),
    //                       height:parseInt($(postcardSpace).css('height'),10)};

    // console.log(spaceDimensions);
    // let randomX = Math.random()* spaceDimensions.width - 200;
    // let randomY = Math.random()* spaceDimensions.height - 200;
    // $(postcardEl).css("top",randomY);
    // $(postcardEl).css("left",randomX);
    // window.setTimeout(function(){
    //   $(postcardEl).addClass('displayed');
    // },400)
    
  },
  methods:{
    selectPostcard(){
      console.log("selectingThisPostcard");
      if (this.selectedPostcard == false){
        this.selectedPostcard = true;
        let data={postcardEl:this.$refs.newPostcard, src: this.design.src};
        this.$emit("selectPostcardMessage",data);
        this.flip(true);
        let _this = this;
        setTimeout(function(){
          //  // this.selectedPostcard = true;
          // this.$refs.postcardFront.classList.add("firstFlip");
          // this.$refs.postcardBack.classList.add("firstFlip");
          _this.showOptions = true; 
        },2000)
        // $(this.$refs.postcard).addClass('displayed');
      }
    },
   flip(isFirstFlip){
    console.log(isFirstFlip);
    console.log("flip event");
    //We are currently showing the front, and need to flip it to the back
    if (isFirstFlip){
      this.$refs.postcardFront.classList.add("firstFlip");
      this.$refs.postcardBack.classList.add("firstFlip");       
    } else {
      this.$refs.postcardFront.classList.remove("firstFlip");
      this.$refs.postcardBack.classList.remove("firstFlip");      
    }
    if (!this.flippedToBack){
       console.log('flipping to back');
      this.$refs.postcardFront.classList.remove("flipping-from-back");
      this.$refs.postcardBack.classList.remove("flipping-from-back");
      this.$refs.postcardFront.classList.add("flipping-from-front");
      this.$refs.postcardBack.classList.add("flipping-from-front"); 
      this.flippedToBack = true;
      console.log(this.$refs.postcardFront.classList);
    //Currently showing the back, need to flip it to the front
    } else {
      console.log('flipping to front');
      this.$refs.postcardFront.classList.remove("flipping-from-front");
      this.$refs.postcardBack.classList.remove("flipping-from-front");
      this.$refs.postcardFront.classList.add("flipping-from-back");
      this.$refs.postcardBack.classList.add("flipping-from-back");
      this.flippedToBack = false;
    }

   },
   typeMessage(e){

      console.log("typing message");
      // let value = this.$refs.messageInput.value;,
      console.log(e);
      // let value = e.target.value;
      // if (value != ""){
        console.log("sending out")
        console.log(e.target.value);
        this.$emit('postcardContentMessage',e.target.value);
      // }

   }
  },
  props: {
    fromData: Object,
    toData: Object,
    message: String,
    design: Object,
    canvasData: String,
    unread: Boolean,
    reset: Boolean

  }
}
</script>

